import React from 'react';
import {inject, observer} from 'mobx-react';
import { Popover, Icon, Empty, Badge , Progress, Affix, message, Modal, Button, Input} from 'antd';

import * as  mobx from 'mobx';
import moment from "moment";

const { TextArea } = Input;

@observer
export default class ProgressList extends React.Component {
    constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            visible: false,
            value: ""
        };
    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    // 定位
    scrollToAnchor(key) {
        const {scrollToAnchor} = this.props;
        scrollToAnchor(key);
    }

    showModal = () => {
        this.setState({
            visible: true
        });
    };

    countDown = ()=> {
        let secondsToGo = 3;
        const modal = Modal.success({
            title: '感谢所有人对此项目所付出的努力',
            content: `幸苦了，继续加油！！`,
        });
        const timer = setInterval(() => {
            secondsToGo -= 1;
            modal.update({
                content: `幸苦了，继续加油！！`,
            });
        }, 1000);
        setTimeout(() => {
            clearInterval(timer);
            modal.destroy();
        }, secondsToGo * 1000);
    }

    handleOk = e => {
        if (this.state.value==""||this.state.value==null) {
            message.warn("请填写关闭原因")
            return false;
        }
        const params = {
            projectId: this.store.projectId,
            text: this.state.value
        }
        this.store.closeProject(params).then((res)=>{
            if (res) {
                this.setState({
                    visible: false,
                    value: ""
                }, ()=>{
                    this.countDown();
                    this.props.updateTree();
                    this.store.getStageTree();
                });
            } else {
                message.error("关闭失败")
            }
        });
    };

    onCancel = () => {
        this.setState({
            visible: false,
            value: ""
        });
    }

    onChange = ({ target: { value } }) => {
        this.setState({ value });
    };

    render() {
        const {listData, isShow} = this.props;
        const {state, closeReason} = this.store;
        const content = (
            <div>
                <p>{state==1?"圆满结束，感谢大家的所有付出！":closeReason}</p>
            </div>
        );
        let isAll = mobx.toJS(listData).every(function(computer){  // false 全部完成
            return computer.rate >= 100;
        });
        return (
            <div>
                <Affix offsetTop={1}>
                    <div className="w clearfix pr"
                         style={{backgroundColor: '#FFF',
                             boxShadow: "0 1px 1px rgba(24,144,255, 0.15), 0 2px 6px 0 rgba(24,144,255, 0.45)",
                             minHeight: '30px',
                             padding: '10px 30px 7px 30px'}}>
                        {mobx.toJS(listData).length>0?mobx.toJS(listData).map((item, index) => {
                            return (
                                <div
                                    onClick={this.scrollToAnchor.bind(this, item.key)}
                                    key={index}
                                    style={{
                                        width: '14%',
                                        float: 'left',
                                        display: 'inlineBlock',
                                        position: 'relative'
                                    }}>
                                    {item.rate>0?
                                        <Badge className="worktile_progress_span_active" status="processing"/>:
                                        <span className={state==2?"worktile_progress_span worktile_progress_span_close": "worktile_progress_span"}/>}
                                    <Progress
                                        className={state==2?"worktile_progress_close worktile_progress":"worktile_progress"}
                                        style={{
                                            display: 'inlineBlock',
                                            paddingLeft: '9px'
                                        }}
                                        size="small"
                                        strokeLinecap="square"
                                        percent={item.rate==null?0:item.rate}/>
                                    <div style={{textAlign: 'left', position: 'relative', left: '-20px'}}>
                                        <p style={{cursor:"pointer"}} className={item.rate>0?"worktile_progress_item_active":"worktile_progress_item"}>
                                            {item.stageName}
                                            {item.stageName=="方案设计"&&item.indexs>=2?<Badge
                                                count={item.indexs}
                                                className="ml5"
                                                style={{ cursor: 'default', backgroundColor: '#fff', color: '#999', boxShadow: '0 0 0 1px #d9d9d9 inset' }}
                                            />:null}
                                        </p>
                                        <p className="worktile_progress_name" style={{ cursor:"pointer"}}>
                                            {item.masters==null?"":item.masters.length>0&&item.masters.map(item=>item.userName).join("、")}
                                            {item.rate==100?<Icon type="check-circle" style={{paddingLeft: '5px', fontSize: '16px', color: "#108DE9", cursor:"pointer"}}/>:null}
                                        </p>
                                        <p className="worktile_progress_name">{item.finishTime==0||item.finishTime==null?(item.rate>0?<a style={{cursor:"pointer"}}>进行中</a>:null):(item.rate==100?moment(item.finishTime).format("YYYY-MM-DD HH:mm"):<a style={{cursor:"pointer"}}>进行中</a>)}</p>
                                    </div>
                                </div>);
                        }):<Empty description={false} />}
                        <div className={mobx.toJS(listData).length>0?"":"hidden"}
                             style={{
                                 width: '2%',
                                 float: 'left',
                                 display: 'inlineBlock',
                                 position: 'relative'
                             }}>
                            {isAll?<Badge className="worktile_progress_span_active" status="processing"/>:<span className={state==2?"worktile_progress_span worktile_progress_span_close": "worktile_progress_span"}/>}
                            <div style={{textAlign: 'left', position: 'relative', left: '-22px', top: '22px'}}>
                                <p className={isAll?"worktile_progress_item_active":"worktile_progress_item"} style={{width: '80px', cursor:"pointer"}}>全部完成</p>
                            </div>
                        </div>
                        {
                            isShow?null:<span>
                                <Button
                                    onClick={this.showModal}
                                    className={state==0?"fr btn_yellow mt5":"hidden"}
                                    style={{position:"absolute", right:"40px", bottom:"10px"}}
                                    type="primary"
                                    shape="round"
                                    size={"small"}
                                    ghost>
                            关闭项目
                        </Button>
                        <Popover content={content}>
                            <Button className={state==1?"fr mt5":"hidden"}
                                    style={{position:"absolute", right:"40px", bottom:"10px"}}
                                    type="primary"
                                    size={"small"}
                                    shape="round">正常结束</Button>
                        </Popover>
                        <Popover content={content}>
                            <Button className={state==2?"fr btn_yellow_active mt5":"hidden"}
                                    style={{position:"absolute", right:"40px", bottom:"10px"}}
                                    type="primary"
                                    size={"small"}
                                    shape="round">中途关闭</Button>
                        </Popover>
                            </span>
                        }
                    </div>
                </Affix>
                {/*关闭项目*/}
                <Modal
                    title="请填写项目中途关闭的原因"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.onCancel}
                >
                      <TextArea
                          value={this.state.value}
                          onChange={this.onChange}
                          placeholder="请填写原因"
                          autoSize={{ minRows: 3, maxRows: 5 }}
                      />
                </Modal>
            </div>
        );
    }
}







