/*
* 任务拆分组件
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import * as mobx from "mobx";
import {
	Table,
	Input,
	InputNumber,
	notification,
	Card,
	Popconfirm,
	Form,
	Icon,
	Row,
	Col,
	Select,
	message,
	DatePicker
} from 'antd';
import * as utils from "@/utils/utils";
import {v4 as uuidv4} from "uuid";
import PersonAdd2 from "@/components/worktile/PersonAdd";
import moment from 'moment';

const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);
const openNotificationWithIcon = type => {
	notification[type]({
		message: '已用工时不能大于预估工时',
		description:
			'若大于，请先调整预估工时',
		placement: 'bottomRight'
	});
};

// 可编辑表格
class EditableCell extends React.Component {
	state = {
		editing: true,//false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			delete values["beginTime"];
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	};
	// 已用工时
	saveTime = (e) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
			if (e!==null&&e!=="") {
			    if (Number(e)>Number(values.totalHours)) {
					openNotificationWithIcon('warning');
					return false;
				}
			}
			delete values["beginTime"];
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	//日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] = moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	onBlur = () => {
		//this.toggleEdit();
	}
	handleSelChange = (val, e) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (e.length) {
			userIds = e.map((item) => {
				return {userId: item.props.value, userName: item.props.children}
			})
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		handleSave({...record, ...data});
	}
	//
	updatePerson = (users) => {
		const {record, handleSave} = this.props;
		let data = {}
		let userIds;
		if (users.length) {
			userIds = users
		} else {
			userIds = []
		}
		data["masters"] = userIds;
		handleSave({...record, ...data});
	}
	//设置开始日期不能选择的时间
	disabledStartDate = startValue => {
		const {record} = this.props;
		if (!startValue || !record.endTime) {
			return false
		}
		return startValue.valueOf() > record.endTime.valueOf()
	}

	//设置结束不能选择的时间
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() <= record.beginTime.valueOf()
	}

	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title, personData, handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'masters') {  // 负责人
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex].length == 0 ? [] : record[dataIndex].length > 0 && record[dataIndex].map((item) => {
						return item.userId
					}),
				})(
					<div className="w">
						<PersonAdd2
							data={record[dataIndex] == null || record[dataIndex].length == 0 ? [] : record[dataIndex].length > 0 && record[dataIndex]}
							updatePerson={this.updatePerson}
							disabled={!editing}
						/>
					</div>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'name') {  //子任务
			return editing ? (
				<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
						<Input
							defaultValue={record[dataIndex]}
							className="w"
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (
				<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'alreadyHours') {  // 已用工时
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							ref={node => (this.input = node)}
							//onPressEnter={this.save}
							onChange={this.saveTime}
							onBlur={this.saveTime}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}小时
				</div>
			);
		} else if (dataIndex == 'beginTime') { // 开始日期
			// 日期时间戳转换字符串
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<div className="w clearfix" style={{minWidth: '155px'}}>
						<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
							<Icon
								style={{
									width: '35px',
									color: '#F17E7E',
									fontSize: '22px',
									float: 'left',
								}}
								type="minus-circle"/>
						</Popconfirm>
						<DatePicker
							defaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")}
							style={{
								width: 'calc(100% - 35px)',
								float: 'right',
								minWidth: '120px'
							}}
							format="YYYY-MM-DD"
							ref={node => (this.input = node)}
							onChange={this.saveDate}
							//设置开始日期
							disabledDate={this.disabledStartDate}
						/>
					</div>
				)}
			</Form.Item>) : (<div className="w clearfix">
					<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
								width: '35px',
								color: '#F17E7E',
								fontSize: '25px',
								float: 'left',
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							width: 'calc(100% - 35px)',
							float: 'right',
							minHeight: '31px'
						}}
						onClick={this.toggleEdit}
					>
						{children}
					</div>
				</div>
			);
		} else if (dataIndex == 'endTime') { // 截止日期
			// 日期时间戳转换字符串
			const numberTostring = record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						style={{minWidth: '120px'}}
						defaultValue={record[dataIndex] == null || record[dataIndex] == 0 ? "" : moment(numberTostring, "YYYY-MM-DD")}
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate2}
						//设置结束不能选择的时间
						disabledDate={this.disabledEndDate}
					/>
				)}
			</Form.Item>) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'totalHours') {  // 总工时
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<InputNumber
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}小时
				</div>
			);
		} else {  // 备注
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			personData,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
					<EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}


/*
* 拆分子任务
* */
@observer
class TaskSplite extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 子任务的数据源
			count: 0,
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		/*
		* subtask 子任务 nextProps.dataSource
		* */
		if (JSON.stringify(nextProps.dataSource) != JSON.stringify(this.props.dataSource)) {
			this.setState({
				dataSource: nextProps.dataSource == null || nextProps.dataSource.length == 0 ? [] : nextProps.dataSource.map((item, index) => {
					return Object.assign(item, {"key": index})
				}),
				count: nextProps.dataSource === null || nextProps.dataSource.length == 0 ? 0 : nextProps.dataSource.length,
			}, () => {
				nextProps.handleSaveSubTask(this.state.dataSource);
			});
		}
	}

	componentDidMount() {
		this.setState({
			dataSource: this.props.dataSource == null || this.props.dataSource.length == 0 ? [] : this.props.dataSource.map((item, index) => {
				return Object.assign(item, {"key": index})
			}),
			count: this.props.dataSource === null || this.props.dataSource.length == 0 ? 0 : this.props.dataSource.length,
		}, () => {
			this.props.handleSaveSubTask(this.state.dataSource);
		});
		this.props.onTaskRef(this)
	}

	componentDidUpdate(prevProps, prevState) {

	}

	clearState = () => {
		this.setState({
			dataSource: []
		});
	}

	// 删除
	handleDelete = (record) => {
		const {id, key} = record;
		if (id !== "" && id !== null && !id.startsWith("!")) { // 删除原来已有的
			this.store.delNode(id).then((res) => {
				if (res) {
					message.success("删除成功")
					// 更新树
					this.props.updateTree();
					const dataSource = [...this.state.dataSource];
					this.setState({
						dataSource: dataSource.filter(item => item.key !== key)
					}, () => {
						this.props.handleSaveSubTask(this.state.dataSource);
						// 更新tree
						let treeData = JSON.parse(JSON.stringify(this.store.treeData));
						this.store.treeData = utils.updateTreeData(treeData, "del", record);
					});
				} else {
					message.error("删除失败")
				}
			});
		} else { // 删除新增的
			const dataSource = [...this.state.dataSource];
			this.setState({
				dataSource: dataSource.filter(item => item.key !== key)
			}, () => {
				this.props.handleSaveSubTask(this.state.dataSource);
				// 更新tree
				let treeData = JSON.parse(JSON.stringify(this.store.treeData));
				this.store.treeData = utils.updateTreeData(treeData, "del", record);
			});
		}
	};

	handleAdd = () => {
		const {count, dataSource} = this.state;
		const {addRecord} = this.props;
		const newData = Object.assign(addRecord, {"key": count, id: "!" + uuidv4()});
		this.setState({
			dataSource: [...dataSource, newData],
			count: count + 1,
		}, () => {
			this.props.handleSaveSubTask(this.state.dataSource);
			// 更新tree
			let treeData = JSON.parse(JSON.stringify(this.store.treeData));
			this.store.treeData = utils.updateTreeData(treeData, "add", newData);
			// console.log(this.state.dataSource, count, '新增')
		});
	};

	handleSave = row => {
		// 已用工时不能大于总工时
		if (Number(row.alreadyHours)>Number(row.totalHours)) {
			//openNotificationWithIcon('warning');
			return false;
		}
		const newData = [...this.state.dataSource];
		const index = newData.findIndex(item => row.key === item.key);
		const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, () => {
			this.props.handleSaveSubTask(this.state.dataSource);
			let newRow = JSON.parse(JSON.stringify({...row}));
			// 更新tree
			let treeData = JSON.parse(JSON.stringify(this.store.treeData));
			this.store.treeData = utils.updateTreeData(treeData, "update", newRow);
		});
	};

	collapseOnClick = () => {
		let _this = this;
		_this.props.collapseOnChange();
	}

	render() {
		const {dataSource} = this.state;
		const {disabled} = this.props;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columnEle = [
			{
				title: '开始日期',
				dataIndex: 'beginTime',
				width: '14%',
				editable: !disabled,
				align: "center",
				render: (text) => {
					return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
				},
			},
			{
				title: '截止日期',
				dataIndex: 'endTime',
				width: '14%',
				editable: !disabled,
				align: "center",
				render: (text) => {
					return text == null || text == 0 ? "" : moment(text).format("YYYY-MM-DD");
				},
			},
			{
				title: '任务名称',
				dataIndex: 'name',
				width: '14%',
				align: 'center',
				editable: !disabled,
				render: (text,) => (<span>{text}</span>)
			},
			{
				title: '负责人',
				dataIndex: 'masters',
				width: '14%',
				editable: !disabled,
				align: 'center',
				render: (text) => {
					return text == null ? "" : text.map(item => item.userName).join("、");
				},
			},
			{
				title: '预估工时',
				dataIndex: 'totalHours',
				width: '14%',
				editable: !disabled,
				align: 'center',
				render: (text) => (<span>{text}小时</span>)
			},
			{
				title: '已用工时',
				dataIndex: 'alreadyHours',
				width: '14%',
				editable: !disabled,
				align: 'center',
				render: (text, record) => (<span id={record.id}>{text}小时</span>)
			},
			{
				title: '备注',
				dataIndex: 'remark',
				width: '14%',
				editable: !disabled,
				align: 'center',
				render: (text) => (<span>{text}</span>)
			},
		];
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					personData: mobx.toJS(this.store.personData),
					handleSave: this.handleSave,
					handleDelete: this.handleDelete,
				}),
			};
		});
		return (
			<Card bordered style={{width: '100%'}}>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
					className="bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					rowKey={row => row.id}
				/>
				<Icon className={disabled ? "hidden" : ""} onClick={this.handleAdd}
				      style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}} type="plus-circle"/>
				<Row>
					<Col span={24} style={{textAlign: 'right', marginTop: '10px'}}>
						<Icon onClick={this.collapseOnClick}
						      title="收起"
						      type="double-right"
						      className="pointer"
						      style={{color: '#808080'}}
						      rotate={-90}/>
					</Col>
				</Row>
			</Card>
		);
	}
}

export default TaskSplite;





