/*
* 方案设计组件
* */
import React from 'react';
import {Form, Row, Col, Card, Button, message, Menu, Popconfirm, Icon, Dropdown, notification,Modal} from 'antd';
import {inject, observer} from 'mobx-react';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import StepInfo from "./StepInfo";
import * as utils from "@/utils/utils";
import * as mobx from "mobx";
const openNotificationWithIcon = type => {
	notification[type]({
		message: '当前已是该阶段，请选择其他的阶段',
		description:
			'',
		placement: 'topLeft'
	});
};
@Form.create()
@observer
export default class ProjectDesign extends React.Component {
	constructor(props) {
		super(props);
		this.store= this.props.store;
		this.state = {
			userIds: [],
            current: 0,
            stepObj1:{}, //初步设计
            stepObj2:{}, //汇报确定
            stepObj3:{}, //关联任务
            stepObj4:{}, //深化设计
            stepObj5:{}, //图纸确认
            proDesignData:this.props.proDesign,
            hidden:true,
            defaultTree:{},
			deCount: 0, // 记录保存状态
			visible: false, // 菜单状态
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		/*
		* 处理更新树节点   taskc 任务：nextProps.data["children"][0]["children"]
		* */
		if (nextProps.proDesign!==this.props.proDesign) {
            /*根据该节点树型数控制改树结构*/
            this.setState({proDesignData:nextProps.proDesign});
            if(Object.keys(nextProps.proDesign).length!==0&&nextProps.proDesign.children!==null){
                this.setState({
                    stepObj1: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName=="初步设计"))[0]),
                    stepObj2: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName=="汇报确定"))[0]),
                    stepObj3: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName=="关联任务"))[0]),
                    stepObj4: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName=="深化设计"))[0]),
                    stepObj5: Object.assign({},nextProps.proDesign.children.filter((item=>item.stepName=="图纸确认"))[0]),
                })
            }
		}
    }

    componentDidMount() {
		/*
		* 处理更新树节点   taskc 任务：nextProps.data["children"][0]["children"]
		* */
		if (this.props.proDesign) {
            /*根据该节点树型数控制改树结构*/
            this.setState({proDesignData:this.props.proDesign});
            if(Object.keys(this.props.proDesign).length!==0&&this.props.proDesign.children!==null){
                this.setState({
                    stepObj1: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName=="初步设计"))[0]),
                    stepObj2: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName=="汇报确定"))[0]),
                    stepObj3: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName=="关联任务"))[0]),
                    stepObj4: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName=="深化设计"))[0]),
                    stepObj5: Object.assign({},this.props.proDesign.children.filter((item=>item.stepName=="图纸确认"))[0]),
                })
            }
		}
	}

	handleSelChange = (val, e) => {
		let userIds;
		if (e.length) {
			userIds = e.map((item) => {
				return {userId: item.props.value, userName: item.props.children}
			})
		} else {
			userIds = []
		}
		this.setState({
			userIds
		});
		this.props.updateState(userIds);
	}

    // 人员选择更新
    updatePerson = (userIds)=>{
        const {proDesignData} = this.state;
        // 更新当前树
        const currentData = Object.assign({}, proDesignData, {masters:userIds});
        // 更新tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));

        this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
        this.props.updateState(userIds);
    }

	onChange = current => {
		this.setState({current});
	};

	// 方案设计保存
	onSave  = () => {
		const {proDesign, saveNodes} = this.props;
        const params = proDesign;
        // console.log(JSON.stringify(params));
		// saveNodes(params);
    }

    // 保存
	handleSubmit = (e) => {
        e.preventDefault();
        const {proDesign} = this.props;
        let masters = proDesign.masters==null?[]:proDesign.masters;
        if (masters.length==0) {
            message.warn("主负责人是必填项");
            return false;
        }
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                const {saveStage} = this.props;
                // console.log(JSON.stringify(this.state.proDesignData))
                let {children, ...proDesignStageData} = this.state.proDesignData;
                saveStage(proDesignStageData);
            }
        });
    }

    onChangeVis = () => {
        this.setState({
            hidden: !this.state.hidden,
            defaultTree: Object.assign({}, this.state.proDesignData)
        });
    }

    onChangeEdit = () => {
        this.props.updateStateItem("design", {isEdit: false});
        this.setState({
            defaultTree: Object.assign({}, this.state.proDesignData)
        });
    }

    // 取消
    onCanlc = ()=> {
        this.props.form.resetFields();
        this.props.updateStateItem("design", {isEdit: true});
        // 更新tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
    }

	updatedeCount= (type) => {
		if (type==true) {
			this.setState({
				deCount: this.state.deCount+1
			});
		} else {
			this.setState({
				deCount: this.state.deCount-1
			});
		}
	}

	onVisibleChange = (visible) => {
		this.setState({
			visible: !this.state.visible
		});
	}

    // 新增
    addStage = () => {
        this.store.addStage().then((res)=>{
            if (res) {
                let oldStageId = JSON.parse(JSON.stringify(this.store.currentStageId));  // old
                this.store.oldStageId = oldStageId;
                this.store.currentStageId = res.id; // new
                this.store.getStageList();
                // 更新tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
                // 初始化获取步骤进度信息
                this.store.getStageTree();
            }
        });
    }

    // 删除
    delStage = (param) => {
        let _this = this;
        _this.store.delStage(param).then((res)=>{
            if (res) {
                message.success("删除成功");
                // 删除成功，currentStage为最新的stageId,最新的数据
                //let stageList = JSON.parse(JSON.stringify(_this.store.stageList))
                // const index = stageList.findIndex(item=>item.id==param);
                // stageList.splice(index, 1);
                // const len = stageList.length-1;
                let oldStageId = JSON.parse(JSON.stringify(_this.store.currentStageId));  // old
                _this.store.oldStageId = oldStageId;
                //_this.store.currentStageId = stageList[len].id; // new
                _this.store.currentStageId = res.id; // new
                // 更新方案设计数据
                //this.store.proDesign = Object.assign({}, res);
                // 更新tree
                let treeData = JSON.parse(JSON.stringify(_this.store.treeData));
                _this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
                // 初始化获取步骤进度信息
                _this.store.getStageTree();
                _this.store.getStageList();
                // _this.store.changeStage(stageList[len].id).then((res)=>{
                // 	if(res) {
                // 		// 更新方案设计数据
                // 		_this.store.proDesign = Object.assign({}, res);
                // 		// 初始化获取步骤进度信息
                // 		_this.store.getStageTree();
                // 	}  else {
                // 		message.error("后端数据错误");
                // 	}
                // });
            }
        });
    }

    // 编辑
    changeStage = (param)=> {
        let _this = this;
        const oldStageId = JSON.parse(JSON.stringify(_this.store.currentStageId));  // old
        if (oldStageId==param) {
            openNotificationWithIcon('warning');
            return false;
        }
        if (_this.store.isDesignEdit==false) { // 编辑中状态，增加提示信息，清空数据
            Modal.confirm({
                title: '您确定要切换方案设计吗?',
                content: '未保存的数据,将会被清空',
                okText:"确定",
                cancelText:"取消",
                onOk() {
                    _this.store.oldStageId = oldStageId;
                    _this.store.changeStage(param).then((res)=>{
                        if(res) {
                            _this.store.currentStageId = res.id; // new
                            _this.props.updateStateItem("design", {isEdit: true});
                            // 更新tree
                            let treeData = JSON.parse(JSON.stringify(_this.store.treeData));
                            _this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
	                        // 初始化获取步骤进度信息
	                        _this.store.getStageTree();
                        }
                    });
                },
                onCancel() {

                }
            });
        } else {
            _this.store.oldStageId = oldStageId;
            _this.store.changeStage(param).then((res)=>{
                if(res) {
                    _this.store.currentStageId = res.id; // new
                    // 更新tree
                    let treeData = JSON.parse(JSON.stringify(_this.store.treeData));
                    _this.store.treeData = utils.updateTreeData(treeData, "changeStage", res);
	                // 初始化获取步骤进度信息
	                _this.store.getStageTree();
                }
            });
        }
    }

	render() {
        let _this = this;
		const {visible,deCount, hidden, stepObj1, stepObj2, stepObj3, stepObj4, stepObj5} = _this.state;
        const {proDesign, form, isEdit} = this.props;
        const {store, ...withoutStoreProps} = this.props;
		const {getFieldDecorator} = form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
        };

		const {projectId,stageList, currentStageId} = _this.store;
		let isEdit2 = projectId==null||projectId==""; // 项目id为空时，不可编辑
		const menu = (
			<Menu className={hidden?"hidden":"tc work_menu"} style={{width:'150px'}}>
				{
					mobx.toJS(stageList).map((item, index)=>{
						return (
							<Menu.Item key={index} className={item.id==currentStageId?"work_menu_item work_menu_item_active":"work_menu_item"}>
								<span onClick={this.changeStage.bind(this, item.id)}>{item.stageName}{item.indexs>=2?item.indexs:null}</span>
								{isEdit?null:<Popconfirm title="确定要删除吗?" onConfirm={() => this.delStage(item.id)}>
									<Icon type="close" className={item.indexs>=2?"work_menu_close":"hidden"}/>
								</Popconfirm>}
							</Menu.Item>
						)
					})
				}
				{isEdit?null:<Menu.Divider />}
				<Menu.Item className={isEdit?"hidden":""}>
					<Icon onClick={this.addStage} style={{fontSize:'20px'}} type="plus-circle" title="新增方案设计"/>
				</Menu.Item>
			</Menu>
		);
		return (
			<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 id="items1" className="kanban_title mb20">
						<Dropdown overlay={menu}
							//visible={visible}
							//onVisibleChange={this.onVisibleChange}
							//trigger={['click']}
						>
							<span>方案设计{proDesign.indexs>=2?proDesign.indexs:null}
					            <Icon className={hidden?"hidden":"ml5 pointer"} type="caret-down" />
							</span>
						</Dropdown>
					</h3>
				<span className="worktile_progress_add">
                    {hidden?
                        <Button type="link" disabled={isEdit2?true:false} onClick={this.onChangeVis}>创建</Button>:
                        isEdit? <Button type="link" onClick={this.onChangeEdit}>编辑</Button>:
                            <div>
                                <span onClick={this.onCanlc} className="mr10">取消</span>
	                            <Button
		                            disabled={deCount>0?true:false}
		                            type="link" onClick={this.handleSubmit}>保存</Button>
                            </div>
                    }
                </span>
				<Form className={hidden?"hidden":"clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="主负责人" className="w">
								{getFieldDecorator('masters', {
                                    initialValue: proDesign.masters==null?[]:proDesign.masters.map((item)=>item.userId),
                                    rules: [{required: false, message: '请选择主负责人'}],
								})(
                                    <PersonAddIcon
                                        data={proDesign.masters==null?[]:proDesign.masters}
                                        updatePerson={this.updatePerson}
                                        disabled={isEdit}
                                    />
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
                <Row className={hidden?"hidden":"mt30 mb30"} >
                    <Col span={24}>
                        <StepInfo
                             key={'step1'}
                             store={this.store}
                             data={stepObj1}
                             stepName={'1. 初步设计'}
                             {...withoutStoreProps}
                             disabled={isEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.oss}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step2'}
                             store={this.store}
                             data={stepObj2}
                             stepName={'2. 汇报确定'}
                             {...withoutStoreProps}
                             disabled={isEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.oss}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step3'}
                             store={this.store}
                             data={stepObj3}
                             stepName={'关联任务'}
                             {...withoutStoreProps}
                             disabled={isEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.oss}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step4'}
                             store={this.store}
                             data={stepObj4}
                             stepName={'3. 深化设计'}
                             {...withoutStoreProps}
                             disabled={isEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.oss}
                             updatedeCount={this.updatedeCount}
                        />
                        <StepInfo
                             key={'step5'}
                             store={this.store}
                             data={stepObj5}
                             stepName={'4. 图纸确认'}
                             {...withoutStoreProps}
                             disabled={isEdit}
                             saveNodes={this.props.saveNodes}
                             saveAttachment={this.props.saveAttachment}
                             delNode={this.props.delNode}
                             oss={this.props.oss}
                             updatedeCount={this.updatedeCount}
                        />
                    </Col>
                </Row>
			</Card>
		)
	}
}




