
/*
* 用途：多选组件参数说明
* 作者：dangw@glodon.com
*
  placeholder: 占位符
  type：Grapbql返回的数据type
  variables：Grapbqls请求的参数变量
  query：Grapbqls请求的参数变量
  url：Grapbqls请求的参数变量
  onChange：组件接受函数
  value：组件接受值
  itemKey: Select组件Option的参数key
  itemValue: Select组件Option的参数value
  body: Grapbqls返回值取值是否是含body
*
* */
import React from 'react';
import {Select} from 'antd';
import PropTypes from 'prop-types';

const Option = Select.Option;

export default class AsyncSelectMuti extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      value: props.value || [],
    };
  }

  componentWillReceiveProps(nextProps) {
    if ('value' in nextProps && this.props.value !== nextProps.value) {
      this.setState({
        value: nextProps.value
      });
    }
  }

  componentDidMount() {
    if (!this.props.url) {
      return;
    }
    //组件初始化
    this.handleFocus();
  }

  handleChange = (val, e) => {
    let {onChange} = this.props;
    if (onChange) {
      onChange(val, e);
    }
  }

  handleFocus = () => {
    const {type,variables,query,url,body}=this.props;
    fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        query: query,
        variables: variables
      })
    })
    .then(response => response.json())
    .then(json => {
      if (json.data!==null&&json.data[type]!==null) { // 容错
        let data;
        if (body!==undefined) {
          data = json.data[type]["body"];
        } else {
          data = json.data[type];
        }
        if (this.props.dataMapFunc) {
          data = data.map(item => this.props.dataMapFunc(item));
        }
        this.setState({
          data
        });
      } else {
        this.setState({
          data: []
        });
      }
    });
  }

  render() {
    const {data} = this.state;
    const {value,itemKey,itemValue,placeholder,style,disabled,ref,onBlur}=this.props;
    const options = data.length&&data.map((item, index)=> <Option key={index} value={item[itemKey]} val={item}>{item[itemValue]}</Option>);

    return (
      <Select
        mode="multiple"
        placeholder={placeholder||"请选择"}
        style={style}
        value={value}
        onChange={this.handleChange}
        onFocus={this.handleFocus}
        onBlur={onBlur}
        disabled={disabled}
        ref={ref}
      >
        {options}
      </Select>
    );
  }
}

AsyncSelectMuti.propTypes = {
  style: PropTypes.object,
  placeholder: PropTypes.string,
  value: PropTypes.string,
  url: PropTypes.string,
  dataMapFunc: PropTypes.func,
  onChange: PropTypes.func,
};



