/*
* 通用树型结构渲染
* dangw@glodon.com
* */
import React from 'react';
import {inject, observer} from 'mobx-react';
import {Card, Form, Row, Col, Button, message} from 'antd';
import EditableTable from "@/components/worktile/EditableTable";
import MyUpload from "@/components/worktile/MyUpload";
import * as utils from "@/utils/utils";
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import * as mobx from "mobx/lib/mobx";

let flatData = [];   // 用于存储递归结果（扁平数据）

@Form.create()
@observer
class WortileCommon extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],   // 任务的数据源
			userIds:[],       // 存储主负责任人数据
            hidden: true,  // 面板折叠展开
            currentTree: {}, // 存储当前组件树对象
            pid: '',     // 父节点id
            projectId: "", // 项目id
            isEdit: false,
            defaultTree: {}, // 记录当前树的默认数据
        };
	}

	componentWillReceiveProps(nextProps, nextContext) {
		/*
		* 处理更新树节点   taskc 任务：nextProps.data["children"][0]["children"]
		* */
		if (nextProps.treeData!==this.props.treeData) {
			/*根据该节点树型数控制改树结构*/
			if (nextProps.treeData.children===null) { // 没有任务集合
				this.setState({
					dataSource: []
				});
			} else {  // 包含任务集合
                const dataSource = nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].children;
                this.setState({
                    dataSource,
                    currentTree: Object.assign({}, nextProps.treeData),   // 更新同步
                    pid: nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].id,
                    projectId: nextProps.treeData.children.filter((item)=>item.treeType=="taskc")[0].projectId,
                }
                , ()=>{
                        // 更新记录当前树的附件数据
                        let attc = nextProps.treeData.children.filter((item)=>item.treeType=="attc")[0]
                        if (Object.keys(this.state.defaultTree).length>0) {
                            // 更新tree
                            let defaultTree = JSON.parse(JSON.stringify(this.state.defaultTree));
                            this.setState({
                                defaultTree: utils.updateTreeData(defaultTree, "update", attc)
                            });
                        }
                })
			}
		}
	}

	componentDidMount() {
		/*
		* 处理树节点   taskc 任务：nextProps.data["children"][0]["children"]
		* */
		if (this.props.treeData) {
			/*根据该节点树型数控制改树结构*/
			if (this.props.treeData.children===null) { // 没有任务集合
				this.setState({
					dataSource: []
				});
			} else {  // 包含子任务集合
				const dataSource = this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].children;
				this.setState({
					dataSource,
                    currentTree: Object.assign({}, this.props.treeData),
                    pid: this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].id,
                    projectId: this.props.treeData.children.filter((item)=>item.treeType=="taskc")[0].projectId,
				})
			}
		}
	}

	handleSelChange = (val, e) => {
		let userIds;
		if (e.length) {
			userIds = e.map((item)=>{return {userId: item.props.value, userName: item.props.children}})
		} else {
			userIds  = []
		}
		this.setState({
			userIds
		});
		const {currentTree} = this.state;
		// 更新当前树
        const currentData = Object.assign({}, currentTree, {masters:userIds});
        this.setState({
            currentTree: Object.assign({}, currentTree, currentData)
        });
    }

	// 保存
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {currentTree, defaultTree} = _this.state;
		// 校验1：任务名称必输
		let currentDataSource = currentTree.children!=null&&currentTree.children[0].children!=null?currentTree.children[0].children:[];
		const isSub = currentDataSource.filter((item)=>{return item.name==""||item.name==null})
		if (isSub.length>0) {  // true
			message.warn("任务名称是必填项");
			return false;
		}
        // 校验2：主任务内容有没有变动，有则主负责人必输
        // 记录之前的主任务数据
        let defaultSource=defaultTree.children!=null&&defaultTree.children[0].children!=null?defaultTree.children[0].children.map(item=>{
            return {
                "name": item.name,
                "masters": item.masters,
	            "beginTime": item.beginTime,
                "endTime": item.endTime,
                "rate": item.rate
            }
        }):[];
        let currentSource = currentTree.children!=null&&currentTree.children[0].children!=null?currentTree.children[0].children.map(item=>{
            return {
                "name": item.name,
                "masters": item.masters,
	            "beginTime": item.beginTime,
                "endTime": item.endTime,
                "rate": item.rate
            }
        }):[];
        const isChange = JSON.stringify(defaultSource)==JSON.stringify(currentSource); // 主任务是否变动
        const isMasters = currentTree&&currentTree.masters==null?[]:currentTree.masters; //是否有主负责人
        if (isMasters.length==0&&isChange==false) {
            message.warn("主负责人是必填项");
            return false;
        }
        _this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                _this.props.saveNodes(currentTree);
                this.setState({
                    isEdit: true
                })
            }
        });
	}

	convert2treeData=(source, id, parentId, children)=>{
		let cloneData = JSON.parse(JSON.stringify(source))
		return cloneData.filter(father=>{
			let branchArr = cloneData.filter(child => father[id] == child[parentId]);
			branchArr.length>0 ? father[children] = branchArr : ''
			return father[parentId] == this.props.treeData.pid        // rootid
		})
	}

	// 递归函数
	convert2flatData = (source)=>{
		source.forEach(el=>{
			let {children, ...item} = el;
			flatData.push(item);
			el.children && el.children.length>0 ? this.convert2flatData(el.children) : ""        // 子级递归
		})
	}

	// 树型数据转扁平数组
	treeData2FlatData = (source) => {
		let {children, ...item} = source;
		flatData.push(item);
		this.convert2flatData(children);
	}

	// 去重
	unique=(data)=>{
		let result = {};
		let finalResult=[];
		for(let i=0;i<data.length;i++){
			result[data[i].id]=data[i];
		}
		for(let item in result){
			finalResult.push(result[item])
		}
		return finalResult;
	}


    // 更新该节点树 数据
    saveData2 = (taskdata, key) => {
        const {currentTree} = this.state;

        let newData = [];
        taskdata.forEach(item=>{
            let {key, children, ...obj} = item;
            newData.push(obj);
        });

        let currentTaskC = currentTree.children.filter(item=> item.treeType==='taskc')[0];
        currentTaskC = Object.assign(currentTaskC,{"children":taskdata})

        let stepChildren = currentTree.children;
        const taskcIndex = stepChildren.findIndex(item=> {return item.treeType==='taskc'} )
        stepChildren[taskcIndex] = currentTaskC;
        this.setState({currentTree:Object.assign(currentTree, {"children":stepChildren})});
    }

    onChangeVis = () => {
        this.setState({
            hidden: !this.state.hidden
        });
        // 点击编辑时记录当前树的默认数据
        this.setState({
            defaultTree: Object.assign({}, this.state.currentTree)
        });
    }

    // 取消
    onCanlc = ()=> {
        this.props.form.resetFields();
        // 更新tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
        this.setState({
            isEdit: true
        });
    }

    onChangeEdit = () => {
        // 点击编辑时记录当前树的默认数据
        this.setState({
            isEdit: false,
            defaultTree: Object.assign({}, this.state.currentTree)
        });
    }

    // 人员选择更新
    updatePerson = (userIds)=>{
        const {currentTree} = this.state;
        // 更新当前树
        const currentData = Object.assign({}, currentTree, {masters:userIds});
        // 更新tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", currentData);
    }

	render() {
		let _this = this;
		const {getFieldDecorator} = _this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const { dataSource, hidden, isEdit, currentTree } = _this.state;
		const {projectId} = _this.store;
		let isEdit2 = projectId==null||projectId==""; // 项目id为空时，不可编辑
        let attcObj = currentTree.children!=null&&currentTree.children.filter((item)=>{return item.treeType==='attc'})[0];  //附件对象
        const attachments = attcObj.children!=null?attcObj.children.map((vitem) => {
            return Object.assign({}, {
                uid: vitem.id,
                name: vitem.fileName,
                status: 'done',
                url: vitem.filePath,
                fileId: vitem.fileId,
                fileName:vitem.fileName,
                filePath: vitem.filePath,
                id: vitem.id
            })
        }) : [];
        let expandedKeys = mobx.toJS(_this.store.expandedKeys)[this.props.id]!== undefined?mobx.toJS(_this.store.expandedKeys)[this.props.id]:[]
        let isExp = expandedKeys.length>0?true:false;
		return (
			<Card bordered style={{width: '100%'}} className="mt20">
				<h3 id={this.props.rowIndex} className="kanban_title mb20">{_this.props.treeData.stageName}</h3>
                <span className="worktile_progress_add">
                    {hidden?
                        <Button type="link" disabled={isEdit2?true:false} onClick={this.onChangeVis}>创建</Button>:
                        isEdit? <Button type="link" onClick={this.onChangeEdit}>编辑</Button>:
                            <div>
                                <span onClick={this.onCanlc} className="mr10">取消</span>
                                <Button disabled={isExp} type="link" onClick={this.handleSubmit}>保存</Button>
                            </div>
					}
                </span>
                <Form className={hidden?"hidden":"clearfix"} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="主负责人" className="w">
								{getFieldDecorator('masters', {
                                    initialValue: currentTree.masters==null?[]:currentTree.masters.map((item)=>item.userId),
                                    rules: [{required: false, message: '请选择主负责人'}],
								})(
                                    <PersonAddIcon
                                        data={currentTree&&currentTree.masters==null?[]:currentTree.masters}
                                        updatePerson={this.updatePerson}
                                        disabled={isEdit}
                                    />
								)}
							</Form.Item>
						</Col>
					</Row>
				</Form>
                {/*任务*/}
                {hidden?null:
                    <EditableTable
                        id={this.props.id}
                        dataSource={dataSource}
                        saveData={this.saveData2}
                        disabled={isEdit}
                        projectId={this.state.projectId}
                        pid={this.state.pid}
                        store={this.store}
                    />}
                {hidden?null:
                    <div className={isEdit?"":"myupload_pos"}>
                    <MyUpload
                    saveAttachment={this.props.saveAttachment}
                    delNode={this.props.delNode}
                    fileList={attachments}
                    pid={attcObj.id}
                    projectId={attcObj.projectId}
                    disabled={isEdit}
                    ossData={this.props.oss}
                    /></div>}
			</Card>
		);
	}
}


export default WortileCommon;



