import React from 'react';
import {inject, observer} from 'mobx-react';
import {Card, Icon, message, Tooltip} from 'antd';
import EditableTable from "@/components/worktile/EditableTable";
import EditableTable2 from "@/components/product/EditableTable2";
import ProposalAttc from "./ProposalAttc";
import * as utils from "@/utils/utils";
import MyUpload from "@/components/worktile/MyUpload";
import * as mobx from "mobx";

@observer
export default class StepInfo extends React.Component {
	constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            option:null,
            current: 0,
            isEdit:false,
            data:this.props.data,
            defaultTree: {}, // 记录默认数据
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.data != this.props.data) {
           this.setState({data:nextProps.data},()=>{
               let stepChildren = nextProps.data.children!=null || typeof(nextProps.data.children)!=="undefined" ? nextProps.data.children:null;
               // 更新记录当前树的附件数据
               let attc = stepChildren.filter((item)=>{return item.treeType==='attc'})[0]
               if (Object.keys(this.state.defaultTree).length>0&&attc!==undefined) {
                   // 更新tree
                   let defaultTree = JSON.parse(JSON.stringify(this.state.defaultTree));
                   this.setState({
                       defaultTree: utils.updateTreeData(defaultTree, "update", attc)
                   });
               }
           })
        }
    }

    // 保存步骤
    saveStep =() => { //这里调用saveNodes
       const {saveNodes} = this.props;
       const {data} = this.state;
       // 校验1：任务名称必输
       let currentDataSource = data.children!=null&&data.children[0].children!=null?data.children[0].children:[];
       const isSub = currentDataSource.filter((item)=>{return item.name==""||item.name==null})
       if (isSub.length>0) {  // true
          message.warn("任务名称是必填项");
          return false;
       }
       saveNodes(data);
       this.onChangeVis();
    }

    // 更新该节点树 数据
    saveData = (taskdata, key) => {
        const {data} = this.state;
        let newData = [];
        taskdata.forEach(item=>{
            let {key, children, ...obj} = item;
            newData.push(obj);
        })

        let currentTaskC = data.children.filter(item=> item.treeType==='taskc')[0];
        currentTaskC = Object.assign(currentTaskC,{"children":taskdata})

        let stepChildren = data.children;
        const taskcIndex = stepChildren.findIndex(item=> {return item.treeType==='taskc'} )
        stepChildren[taskcIndex] = currentTaskC;
        // console.log(Object.assign(data, {"children":stepChildren}))
        this.setState({data:Object.assign(data, {"children":stepChildren})});

        // saveTasks(newData);
    }

    saveProposal =(proposalData) => {
        const {data} = this.state;
        let currentProposal = data.children.filter(item=> item.treeType==='proposal')[0];
        currentProposal = Object.assign(currentProposal,proposalData);

        let stepChildren = data.children;
        const proposalIndex = stepChildren.findIndex(item=> {return item.treeType==='proposal'} )
        stepChildren[proposalIndex] = currentProposal;

        this.setState({data:Object.assign(data, {"children":stepChildren})});

    }

    onChangeVis = () => {
        this.setState({
            isEdit: !this.state.isEdit
        }, ()=>{
            // 点击编辑，记录当前树
            if (this.state.isEdit==true) {
                this.setState({
                    defaultTree: Object.assign({}, this.state.data)
                }, ()=>{
                    // 同步大阶段的保存状态
                    this.props.updatedeCount(true);
                });
            } else { // 取消
                // 更新tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
                // 同步大阶段的保存状态
                this.props.updatedeCount(false);
            }
        });
    }

    render() {
        let _this = this;
        const { data, isEdit,current } = _this.state;
        const  {stepName, updateTree, disabled, oss} = _this.props;
        let currentDataSource = data.children!=null&&data.children[0].children!=null?data.children[0].children:[];
        let stepChildren = data.children!=null || typeof(data.children)!=="undefined" ? data.children:null;
        let proposalIndex = -1;
        let attcIndex = -1;
        if(stepChildren!=null) {
            proposalIndex = stepChildren.findIndex((item)=>{return item.treeType==='proposal'}); //是否有报价
            attcIndex = stepChildren.findIndex((item)=>{return item.treeType==='attc'}); //是否有内容留存
        }
        let attcObj;
        let attachments;
        if (stepName=="关联任务"&&attcIndex!=-1) {  // 是否有关联任务
           attcObj = stepChildren.filter((item)=>{return item.treeType==='attc'})[0];//附件对象
           attachments = attcObj&&attcObj.children&&attcObj.children!=null?attcObj.children.map((vitem) => {
                return Object.assign({}, {
                    uid: vitem.id,
                    name: vitem.fileName,
                    status: 'done',
                    url: vitem.filePath,
                    fileId: vitem.fileId,
                    fileName:vitem.fileName,
                    filePath: vitem.filePath,
                    id: vitem.id
                })
            }) : [];
        }
        let expandedKeys = mobx.toJS(_this.store.expandedKeys)[data.id]!== undefined?mobx.toJS(_this.store.expandedKeys)[data.id]:[]
        let isExp = expandedKeys.length>0?true:false;
		return (
            <div className={"mb15"}>
                <div className={"mb15"}>
                    <span className="mr20">{stepName=="关联任务"? <Icon type="link" className="mr10"/>: null}{stepName}</span>
                    {!isEdit ?
                    <span>
                        {/*{!disabled?:null}*/}
                        <Tooltip placement="bottom" title={"编辑"}>
                            <Icon
                                onClick={_this.onChangeVis}
                                style={{fontSize:'20px', marginRight:'10px'}}
                                type="form" />
                        </Tooltip>
                    </span>
                    :
                    <span>
                        <Tooltip placement="bottom" title={"取消"}>
                            <Icon
                                onClick={this.onChangeVis}
                                style={{fontSize:'20px', marginRight:'10px'}}
                                type="close-circle" />
                        </Tooltip>
                        <Tooltip placement="bottom" title={"保存"}>
                            {isExp? <Icon
                                style={{fontSize:'20px', color:"#e6e6e6"}}
                                type="check-circle" />: <Icon
                                onClick={_this.saveStep}
                                style={{fontSize:'20px'}}
                                type="check-circle" />}
                        </Tooltip>
                    </span>
                    }
                </div>
                {stepName=="关联任务"?
                <EditableTable
                    id={data.id}
                    store={_this.store}
                    masters={data.masters==null?[]:data.masters}
                    dataSource={currentDataSource}
                    saveData={_this.saveData}
                    disabled={!isEdit}
                    projectId={data.projectId}
                    pid={data.children!=null ? data.children[0]['id']:''}
                />:
                <EditableTable2
                    id={data.id}
                    store={_this.store}
                    masters={data.masters==null?[]:data.masters}
                    dataSource={currentDataSource}
                    oss={oss}
                    taskhaveattt={true}
                    saveData={_this.saveData}
                    disabled={!isEdit}
                    delNode={this.props.delNode}
                    saveAttachment={this.props.saveAttachment}
                    projectId={data.projectId}
                    pid={data.children!=null ? data.children[0]['id']:''}
                />}
                {proposalIndex!=-1?
                    <ProposalAttc
                        store={_this.store}
                        stepName = {stepName}
                        disabled={!isEdit}
                        saveProposal= {this.saveProposal}
                        saveNodes={this.props.saveNodes}
                        saveAttachment={this.props.saveAttachment}
                        delNode={this.props.delNode}
                        oss={oss}
                        data={stepChildren.filter((item)=>{return item.treeType==='proposal'})}
                    /> :null}
                {stepName=="关联任务"&&attcIndex!=-1?
                    <div className={!isEdit?"mt15":"myupload_pos"}>
                    <MyUpload
                        saveAttachment={this.props.saveAttachment}
                        delNode={this.props.delNode}
                        fileList={attachments}
                        pid={attcObj?attcObj.id:""}
                        projectId={attcObj?attcObj.projectId:""}
                        disabled={!isEdit}
                        ossData={oss}
                    /></div>:null}
            </div>
		)
	}
}




