/*
* 项目信息 新的模板
* dangw@glodon.com
* */
import React from 'react';
import {Input, Form, Row, Col, Card, Button, Tabs, InputNumber, DatePicker, Select} from 'antd';
import AsyncSelectMuti from "@/components/common/AsyncSelectMuti";

import Config from "@/config";
import Schemas from "@/utils/Schemas/bpm";
import PersonAddIcon from "@/components/worktile/PersonAddIcon";
import MyUpload from './MyUpload';
import moment from 'moment';

const {TextArea} = Input;
const {TabPane} = Tabs;
const {Option} = Select;
const obj = {2: ["5"], 4: ["4"], 5: ["6"]}; // 处理产品类型

@Form.create()
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
            fields: {
            	"id":"",
	            "pid":"",
	            "projectId":"",
	            "code":"",
	            "name":"",
	            "masters":[],
	            "customer":"",
	            "productType":[],
	            "address":"",
	            "description":"",
	            "milepost":"",
	            "builtArea":"",
	            "totalCost":"",
	            "builtType":"",
	            "beginTime": null,
	            "endTime": null,
	            "constructionUnit":"",
	            "constructionQualification":"",
	            "partyaUnit":"",
	            "unitQualification":"",
	            "declarationAwards":"",
	            "awardGrade":"",
	            "buySoftware":"",
	            "usageSituation":"",
	            "background":"",
	            "temporaryDecision":"",
	            "temporaryManagerDesire":"",
	            "temporaryRequirement":"",
	            "temporaryBudgetStandard":"",
	            "temporaryTotalAcceptability":"",
	            "temporaryTotalRange":"",
	            "temporaryBudget":"",
	            "temporaryPriceDesire":"",
	            "temporaryEstimatePrice":"",
	            "temporaryManagersNumber":"",
	            "temporaryWorkersNumber":"",
	            "temporaryBeginTime": null,
	            "temporaryEndTime":null,
	            "temporaryBudgetEvaluation":"",
            },  // 存储当前组件的数据
            hidden: true,  // 面板是否折叠展开
			count: 1,    // 记录创建还是编辑
			key: "1", // 当前key
		};
	}

	componentWillReceiveProps(nextProps,nextContext){
		const {itemObj} = nextProps;
		if (itemObj) {
			if (JSON.stringify(itemObj)!==JSON.stringify(this.props.itemObj)) {
				this.setState({
					fields: Object.assign({}, itemObj.children.filter((item)=>{ return item.treeType=="projectinfo"})[0]),
				});
			}
		}
	}

	componentDidMount() {
		const {itemObj, count, hidden} = this.props;
		this.setState({
			fields: Object.assign({}, itemObj.children.filter((item) => {
				return item.treeType == "projectinfo"
			})[0]),
			count,
			hidden
		});
		this.props.onRef(this);
	}

	callback = (key) => {
		this.setState({
			key
		});
	}

	// 人员选择更新
	updatePerson = (userIds)=>{
		let fields = JSON.parse(JSON.stringify(this.state.fields));
		this.setState({
			fields: Object.assign(fields, {
				"masters": userIds
			}),
		});
	}

	// 编辑、创建
	onChangeVis = () => {
		this.setState({
			hidden: !this.state.hidden,
			count: Number(this.state.count)+1
		});
		this.props.updateCount('count', Number(this.props.count) + 1)
	}

	// 取消
	onCanlc = ()=> {
		this.props.form.resetFields();
		this.setState({
			hidden: !this.state.hidden,
			key: "1"
		});
	}

	// 保存
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {fields} = _this.state;
		_this.props.form.validateFields((err, values) => {
			if (!err) {
				delete values.masters;
				const params = Object.assign(fields, values,
					  {
						"projectType": this.props.projectType,
					  	"productType": values.productType.map((item)=> item.toString()),
						"beginTime":  values['beginTime']==null||values['beginTime']==""?0:moment(values['beginTime'].format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf(),
						"endTime": values['endTime']==null||values['endTime']==""?0:moment(values['endTime'].format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf(),
						"temporaryBeginTime":  values['temporaryBeginTime']==null||values['temporaryBeginTime']==""?0:moment(values['temporaryBeginTime'].format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf(),
						"temporaryEndTime":  values['temporaryEndTime']==null||values['temporaryEndTime']==""?0:moment(values['temporaryEndTime'].format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf(),
					  });
				if (params.children==null) {
					delete params.children;
				}
				_this.props.saveProject(params);
			}
		});
	}

	render() {
		const {fields, hidden, key} = this.state;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 10},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 14},
			},
		};
		const formItemLayout2 = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 12},
				md: {span: 10},
				lg: {span: 10}
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 12},
				md: {span: 14},
				lg: {span: 14}
			},
		};
		const {itemObj, state, ossData, saveAttachment, delNode, count, projectType} = this.props;
		let attcObj = itemObj.children!=null&&itemObj.children.filter((item)=>{return item.treeType==='attc'})[0];  //附件对象
		const attachments = attcObj.children!=null?attcObj.children.map((vitem) => {
			return Object.assign({}, {
				uid: vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.fileId,
				fileName:vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id
			})
		}) : [];
		const temID = {}
		return (
			<Card bordered className="pr" style={{width: '100%'}}>
				<h3 id={itemObj.id||"items0"} className="kanban_title mb20">项目信息</h3>
				<span className="worktile_progress_add">
                    {hidden?
                        <Button className={state==2?"hidden":""} type="link" onClick={this.onChangeVis}>{count==1?"创建":"编辑"}</Button>:
                        <div>
                            <span onClick={this.onCanlc} className="mr10">取消</span>
                            <span onClick={this.handleSubmit}>保存</span>
                        </div>}
                </span>
				<Tabs className={count==1?"hidden":"item_tabs"} activeKey={key} onChange={this.callback}>
					<TabPane tab="主体项目信息" key="1">
						<Form layout="inline" {...formItemLayout}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="项目名称" className="w">
										{getFieldDecorator('name', {
											initialValue: fields.name||"",
											rules: [{required: true, message: '请输入项目名称'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="主负责人" className="w">
										{getFieldDecorator('masters', {
											initialValue: fields.masters==null?[]:fields.masters,
											rules: [{required: false, message: '请输入主负责人'}],
										})(
											<PersonAddIcon
												data={fields.masters==null?[]:fields.masters}
												updatePerson={this.updatePerson}
												disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="客户名称" className="w">
										{getFieldDecorator('customer', {
											initialValue: fields.customer||"",
											rules: [{required: false, message: '请输入客户名称'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xl={16}>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="产品类型" className="w">
											{getFieldDecorator('productType', {
												initialValue: projectType==4||projectType==5?obj[projectType]:(fields.productType==null?[]:fields.productType),
												rules: [{required: false, message: '请选择产品类型'}],
											})(
												<AsyncSelectMuti
													className="w"
													type="getProductTypeList"
													variables={{}}
													query={Schemas.bpm.getProductTypeList}
													url={Config.bpm.getProductTypeList}
													value={projectType==4||projectType==5?obj[projectType]:fields.productType==null?[]:fields.productType}
													itemKey="id"
													itemValue="typeName"
													disabled={hidden||projectType==4||projectType==5}
												/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="项目地点"  className="w">
											{getFieldDecorator('address', {
												initialValue: fields.address||"",
												rules: [{required: false, message: '请输入项目地点'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label={<span>建筑面积(㎡)</span>}  className="w">
											{getFieldDecorator('builtArea', {
												initialValue: fields.builtArea||"",
												rules: [{required: false, message: '请输入建筑面积'}],
											})(
												<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="总造价(万元)"  className="w">
											{getFieldDecorator('totalCost', {
												initialValue: fields.totalCost||"",
												rules: [{required: false, message: '请输入总造价'}],
											})(
												<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
											)}
										</Form.Item>
									</Col>
								</Col>
								<Col xl={8}>
									<Col xs={24} sm={24} md={18} lg={12} xl={24}>
										<Form.Item label="项目概况" className="w">
											{getFieldDecorator('description', {
												initialValue: fields.description||"",
												rules: [{required: false, message: '请输入项目概况'}],
											})(
												<TextArea rows={4} disabled={hidden} placeholder="请输入" />
											)}
										</Form.Item>
									</Col>
								</Col>
							</Row>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="建筑类型" className="w">
										{getFieldDecorator('builtType', {
											initialValue: fields.builtType||"",
											rules: [{required: false, message: '请输入建筑类型'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="开工日期" className="w">
										{getFieldDecorator('beginTime', {
											initialValue: fields.beginTime==null||fields.beginTime==0?"":moment(moment(fields.beginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
											rules: [{required: false, message: '请选择开工日期'}],
										})(
											<DatePicker
												className="w"
												placeholder="请选择"
												format="YYYY-MM-DD"
												disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="竣工日期" className="w">
										{getFieldDecorator('endTime', {
											initialValue: fields.endTime==null||fields.endTime==0?"":moment(moment(fields.endTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
											rules: [{required: false, message: '请选择竣工日期'}],
										})(
											<DatePicker
												format="YYYY-MM-DD"
												className="w"
												placeholder="请选择"
												disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
							</Row>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xl={16}>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="施工单位" className="w">
											{getFieldDecorator('constructionUnit', {
												initialValue: fields.constructionUnit||"",
												rules: [{required: false, message: '请输入施工单位'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="施工资质"  className="w">
											{getFieldDecorator('constructionQualification', {
												initialValue: fields.constructionQualification||"",
												rules: [{required: false, message: '请输入施工资质'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="甲方单位"  className="w">
											{getFieldDecorator('partyaUnit', {
												initialValue: fields.partyaUnit||"",
												rules: [{required: false, message: '请输入甲方单位'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="甲方资质"  className="w">
											{getFieldDecorator('unitQualification', {
												initialValue: fields.unitQualification||"",
												rules: [{required: false, message: '请输入甲方资质'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
								</Col>
								<Col xl={8}>
									<Col xs={24} sm={24} md={18} lg={12} xl={24}>
										<Form.Item label="关键里程碑" className="w">
											{getFieldDecorator('milepost', {
												initialValue: fields.milepost||"",
												rules: [{required: false, message: '请输入关键里程碑'}],
											})(
												<TextArea rows={4} disabled={hidden} placeholder="请输入" />
											)}
										</Form.Item>
									</Col>
								</Col>
							</Row>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xl={16}>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="申报奖项" className="w">
											{getFieldDecorator('declarationAwards', {
												initialValue: fields.declarationAwards||"",
												rules: [{required: false, message: '请输入申报奖项'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="奖项等级"  className="w">
											{getFieldDecorator('awardGrade', {
												initialValue: fields.awardGrade||"",
												rules: [{required: false, message: '请输入奖项等级'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="已采购软件"  className="w">
											{getFieldDecorator('buySoftware', {
												initialValue: fields.buySoftware||"",
												rules: [{required: false, message: '请输入已采购软件'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
									<Col xs={24} sm={24} md={18} lg={12} xl={12}>
										<Form.Item label="使用情况"  className="w">
											{getFieldDecorator('usageSituation', {
												initialValue: fields.usageSituation||"",
												rules: [{required: false, message: '请输入使用情况'}],
											})(
												<Input placeholder="请输入" disabled={hidden} />
											)}
										</Form.Item>
									</Col>
								</Col>
								<Col xl={8}>
									<Col xs={24} sm={24} md={18} lg={12} xl={24}>
										<Form.Item label="项目背景及意义" className="w">
											{getFieldDecorator('background', {
												initialValue: fields.background||"",
												rules: [{required: false, message: '请输入项目背景及意义'}],
											})(
												<TextArea rows={4} disabled={hidden} placeholder="请输入" />
											)}
										</Form.Item>
									</Col>
								</Col>
							</Row>
						</Form>
					</TabPane>
					<TabPane tab="临建项目信息" key="2">
						<Form layout="inline" {...formItemLayout2}>
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="决策方式" className="w">
										{getFieldDecorator('temporaryDecision', {
											initialValue: fields.temporaryDecision||"",
											rules: [{required: false, message: '请输入决策方式'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="项目经理意愿" className="w">
										{getFieldDecorator('temporaryManagerDesire', {
											initialValue: fields.temporaryManagerDesire||"",
											rules: [{required: false, message: '请输入项目经理意愿'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="特殊要求" className="w">
										{getFieldDecorator('temporaryRequirement', {
											initialValue: fields.temporaryRequirement||"",
											rules: [{required: false, message: '请输入特殊要求'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="预算标准" className="w">
										{getFieldDecorator('temporaryBudgetStandard', {
											initialValue: fields.temporaryBudgetStandard||"",
											rules: [{required: false, message: '请输入预算标准'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="总承包形式接受度" className="w">
										{getFieldDecorator('temporaryTotalAcceptability', {
											initialValue: fields.temporaryTotalAcceptability||"",
											rules: [{required: false, message: '请输入总承包形式接受度'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="总承包范围" className="w">
										{getFieldDecorator('temporaryTotalRange', {
											initialValue: fields.temporaryTotalRange||"",
											rules: [{required: false, message: '请输入总承包范围'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="客户需求预算价(万元)" className="w">
										{getFieldDecorator('temporaryBudget', {
											initialValue: fields.temporaryBudget||"",
											rules: [{required: false, message: '请输入客户需求预算价'}],
										})(
											<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="客户对此价格意愿度" className="w">
										{getFieldDecorator('temporaryPriceDesire', {
											initialValue: fields.temporaryPriceDesire||"",
											rules: [{required: false, message: '请输入客户对此价格意愿度'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="预估客户可接受价格" className="w">
										{getFieldDecorator('temporaryEstimatePrice', {
											initialValue: fields.temporaryEstimatePrice||"",
											rules: [{required: false, message: '请输入预估客户可接受价格'}],
										})(
											<Input placeholder="请输入" disabled={hidden} />
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="管理人员数量(人)" className="w">
										{getFieldDecorator('temporaryManagersNumber', {
											initialValue: fields.temporaryManagersNumber||"",
											rules: [{required: false, message: '请输入管理人员数量'}],
										})(
											<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="劳务人员数量(人)"  className="w">
										{getFieldDecorator('temporaryWorkersNumber', {
											initialValue: fields.temporaryWorkersNumber||"",
											rules: [{required: false, message: '请输入劳务人员数量'}],
										})(
											<InputNumber placeholder="请输入" disabled={hidden} className="w"/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="预算综合判断"  className="w">
										{getFieldDecorator('temporaryBudgetEvaluation', {
											initialValue: fields.temporaryBudgetEvaluation||"",
											rules: [{required: false, message: '请选择预算综合判断'}],
										})(
											<Select
												placeholder="请选择"
												disabled={hidden}
												className="w">
												<Option title="有付费能力，高度重视，强调差异化体现" value="有付费能力，高度重视，强调差异化体现">有付费能力，高度重视，强调差异化体现</Option>
												<Option title="追求普通标准，合适就好" value="追求普通标准，合适就好">追求普通标准，合适就好</Option>
												<Option title="预算有限，临建功能刚需，其余尚可就好" value="预算有限，临建功能刚需，其余尚可就好">预算有限，临建功能刚需，其余尚可就好</Option>
											</Select>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="开工日期"  className="w">
										{getFieldDecorator('temporaryBeginTime', {
											initialValue: fields.temporaryBeginTime==null||fields.temporaryBeginTime==0?"":moment(moment(fields.temporaryBeginTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
											rules: [{required: false, message: '请选择开工日期'}],
										})(
											<DatePicker
												className="w"
												format="YYYY-MM-DD"
												placeholder="请选择"
												disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={8}>
									<Form.Item label="竣工日期"  className="w">
										{getFieldDecorator('temporaryEndTime', {
											initialValue: fields.temporaryEndTime==null||fields.temporaryEndTime==0?"":moment(moment(fields.temporaryEndTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
											rules: [{required: false, message: '请选择竣工日期'}],
										})(
											<DatePicker
												className="w"
												format="YYYY-MM-DD"
												placeholder="请选择"
												disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
							</Row>
						</Form>
					</TabPane>
				</Tabs>
				<div className={count==1||(itemObj.id==null||itemObj.id=="")?"hidden":"pr"}>
					<MyUpload
						saveAttachment={saveAttachment}
						delNode={delNode}
						fileList={attachments}
						disabled={hidden}
						pid={attcObj.id}
						projectId={attcObj.projectId}
						ossData={ossData}
					/>
					<span style={{
						position: 'absolute',
						left: '35px',
						top: '4px',
						fontSize:'12px',
						color: '#999'
					}}>请上传现场平面布置图（含临建区域范围）和拟建建筑效果图（设计理念）</span>
				</div>
			</Card>
		)
	}
}



