/*
* Table组件
* */
import React from 'react';
import {inject, observer} from "mobx-react";
import {Table, Input, DatePicker, Popconfirm, Form, Modal, Icon, Select, message, InputNumber,	Popover} from 'antd';
import TaskSplite from '@/components/worktile/TaskSplite';
import moment from 'moment';
import emitter from "@/utils/events";
import * as  mobx from 'mobx';
import * as utils from '@/utils/utils';
import { v4 as uuidv4 } from 'uuid';
import PersonAdd2 from "@/components/worktile/PersonAdd";
import MyUpload from "@/components/worktile/MyUpload";

let runonlyonce =false;
const { Option } = Select;
const EditableContext = React.createContext();
const EditableRow = ({form, index, ...props}) => (
	<EditableContext.Provider value={form}>
		<tr {...props} />
	</EditableContext.Provider>
);
const EditableFormRow = Form.create()(EditableRow);


/*自定义编辑组件*/
class EditableCell extends React.Component {
	state = {
		editing: true, //false,
		userIds: [],
	};
	toggleEdit = () => {
		const editing = !this.state.editing;
		this.setState({editing}, () => {
			if (editing) {
				this.input.focus();
			}
		});
	};
	save = e => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (error && error[e.currentTarget.id]) {
				return;
			}
            //this.toggleEdit();
			delete values["beginTime"];
			delete values["endTime"];
            delete values["masters"];
            handleSave({...record, ...values});
		});
	};
	//日期格式保存
	saveDate = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('beginTime')) {
				values["beginTime"] =  moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["endTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	saveDate2 = (date, dateString) => {
		const {record, handleSave} = this.props;
		this.form.validateFields((error, values) => {
			if (values.hasOwnProperty('endTime')) {
				values["endTime"] =  moment(dateString, 'YYYY-MM-DD').valueOf();
			}
			delete values["beginTime"];
			delete values["masters"];
			handleSave({...record, ...values});
		});
	}
	handleSelChange = (val, e) => {
		 const { record, handleSave } = this.props;
		 let data = {}
         let userIds;
         if (e.length) {
            userIds = e.map((item)=>{return {userId: item.props.value, userName: item.props.children}})
         } else {
            userIds  = []
         }
		 data["masters"] = userIds;
		 handleSave({ ...record, ...data });
	}

	//
    updatePerson = (users) => {
        const { record, handleSave } = this.props;
        let data = {}
        let userIds;
        if (users.length) {
            userIds = users
        } else {
            userIds  = []
        }
        data["masters"] = userIds;
        handleSave({ ...record, ...data });
    }
	onBlur=()=>{
		//this.toggleEdit();
	}
	//设置开始日期不能选择的时间
	disabledStartDate = startValue => {
		const {record} = this.props;
		if (!startValue || !record.endTime) {
			return false
		}
		return startValue.valueOf() > record.endTime.valueOf()
	}
	//设置结束不能选择的时间
	disabledEndDate = endValue => {
		const {record} = this.props;
		if (!endValue || !record.beginTime) {
			return false
		}
		return endValue.valueOf() <= record.beginTime.valueOf()
	}
	renderCell = form => {
		this.form = form;
		const {children, dataIndex, record, title,personData, handleDelete} = this.props;
		const {editing} = this.state;
		if (dataIndex == 'name') {  // 任务
			return editing ? (
				<Form.Item style={{ margin: 0 }}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: true,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex],
					})(
                        <div className="w clearfix">
                            <Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
                                <Icon
                                    style={{
                                        width: '35px',
                                        color: '#F17E7E',
                                        fontSize: '25px',
                                        float: 'left'
                                    }}
                                    type="minus-circle"/>
                            </Popconfirm>
                            <Input
                                style={{
                                    float: 'right',
                                    width: 'calc(100% - 35px)',
                                }}
                                defaultValue={record[dataIndex]}
                                ref={node => (this.input = node)}
                                //onChange={this.save}
                                onPressEnter={this.save}
                                onBlur={this.save}
                            />
                        </div>
					)}
				</Form.Item>
			): (
				<div className="w clearfix">
					<Popconfirm title="确定要删除吗?" onConfirm={() => handleDelete(record)}>
						<Icon
							style={{
                                width: '35px',
                                color: '#F17E7E',
                                fontSize: '22px',
								float: 'left'
							}}
							type="minus-circle"/>
					</Popconfirm>
					<div
						className="editable-cell-value-wrap"
						style={{
							float: 'right',
                            width: 'calc(100% - 35px)',
                            minHeight: '31px'}}
						onClick={this.toggleEdit}
					>
						{children}
					</div>
				</div>
			);
		} else if (dataIndex == 'masters') {  // 负责人
			return editing ? (<Form.Item style={{margin: 0}} className="w">
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex]==null||record[dataIndex].length==0?[]:record[dataIndex].length>0&&record[dataIndex].map((item)=>{return item.userId}),
				}) (
					<div className="w" ref={node => (this.input = node)}>
                        <PersonAdd2
                            data={record[dataIndex]==null||record[dataIndex].length==0?[]:record[dataIndex].length>0&&record[dataIndex]}
                            updatePerson={this.updatePerson}
                            disabled={!editing}
                        />
					</div>
				)}
			</Form.Item>):(<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}>
                    {children}
                    </div>
			);
		} else if (dataIndex == 'beginTime') { // 开始日期
			// 日期时间戳转换字符串
			const numberTostring = record[dataIndex]==null||record[dataIndex]==0?"":moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex]==null||record[dataIndex]==0?"": moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
						onChange={this.saveDate}
						//设置开始日期
						disabledDate={this.disabledStartDate}
					/>
				)}
			</Form.Item>): (<div
					className="editable-cell-value-wrap"
					style={{ minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		} else if (dataIndex == 'endTime') { // 截止日期
            // 日期时间戳转换字符串
            const numberTostring = record[dataIndex]==null||record[dataIndex]==0?"":moment(record[dataIndex]).format('YYYY-MM-DD');
			return editing ? (<Form.Item style={{margin: 0}}>
				{form.getFieldDecorator(dataIndex, {
					rules: [
						{
							required: false,
							message: `${title}是必输`,
						},
					],
					initialValue: record[dataIndex]==null||record[dataIndex]==0?"": moment(numberTostring, "YYYY-MM-DD")
				})(
					<DatePicker
						format="YYYY-MM-DD"
						ref={node => (this.input = node)}
                        onChange={this.saveDate2}
						//设置结束不能选择的时间
						disabledDate={this.disabledEndDate}
					/>
				)}
			</Form.Item>): (<div
					className="editable-cell-value-wrap"
					style={{ minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
                    {children}
				</div>
			);
		} else if (dataIndex == 'rate') {  // 完成比例
			let subTasks = record.children!=null&&record.children.filter((item)=>{return item.treeType==='subtask'})
			const subNumber = subTasks==null||subTasks.length==0?0:subTasks.length
            const isEdit = subNumber>0?true:false;
            return editing ? (<Form.Item style={{ margin: 0 }}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex]==null||record[dataIndex]==0?"":record[dataIndex],
					})(
						<InputNumber
                            disabled={isEdit}
                            min={0}
                            max={100}
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
						/>
					)}
				</Form.Item>
			): (<div
					className="editable-cell-value-wrap"
                    style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}%
				</div>
			);
		}  else if (dataIndex == 'remark') {  // 备注
			return editing ? (<Form.Item style={{margin: 0}}>
					{form.getFieldDecorator(dataIndex, {
						rules: [{
							required: false,
							message: `${title}是必输`,
						}],
						initialValue: record[dataIndex] == null || record[dataIndex] == 0 ? "" : record[dataIndex],
					})(
						<Input
							ref={node => (this.input = node)}
							onPressEnter={this.save}
							onBlur={this.save}
							placeholder="请输入"
						/>
					)}
				</Form.Item>
			) : (<div
					className="editable-cell-value-wrap"
					style={{minHeight: '31px'}}
					onClick={this.toggleEdit}
				>
					{children}
				</div>
			);
		}
	};

	render() {
		const {
			editable,
			dataIndex,
			title,
			record,
			index,
			handleSave,
			children,
			personData,
			...restProps
		} = this.props;
		return (
			<td {...restProps}>
				{editable ? (
                    <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
				) : (
					children
				)}
			</td>
		);
	}
}

/*
* 编辑table
* */
@observer
class EditableTable2 extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			dataSource: [],  // 任务数据源
            count: 0,        // table的key
            primaryData: {
				"id": "",
				"pid": "",
				"projectId": "",
				"treeType": "task",
				"masters": [],
				"children": null,
				"name": "",
				"rate": 0,
				"nodeCount": null,
	            "beginTime": null,
				"endTime": null,
				"orders": 0,
	            "remark": ""
			}, // 新增任务数据
			expandedKeys: [], // 折叠
			keys: [], // 折叠
			subtasks:[],
            activeObj: {}, // 记录拆解前主任务数据
            activeSub: [], // 记录拆解前子任务数据
			subTaskDis: false, // 控制子任务的编辑状态
		};
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.dataSource!=this.props.dataSource) {
			this.setState({
                dataSource: nextProps.dataSource==null||nextProps.dataSource.length==0?[]:nextProps.dataSource.map((item, index)=>{
                        return Object.assign(item, {key: index})}),
                count: nextProps.dataSource==null||nextProps.dataSource.length==0?0:nextProps.dataSource.length,
                primaryData: Object.assign({}, {
                    "id": "",
                    "pid": nextProps.pid,
                    "projectId": nextProps.projectId,
                    "treeType": "task",
                    "masters": [],
                    "children": null,
                    "name": "",
                    "rate": 0,
                    "nodeCount": null,
	                "beginTime": null,
                    "endTime": null,
                    "orders": 0,
	                "remark": ""
                }) // 新增任务数据
			})
		}
	}

	componentDidMount() {
        this.setState({
            dataSource: this.props.dataSource==null||this.props.dataSource.length==0?[]:this.props.dataSource.map((item, index)=>{
                return Object.assign(item, {"key": index})
            }),
            count: this.props.dataSource===null||this.props.dataSource.length==0?0:this.props.dataSource.length,
            primaryData: Object.assign({}, {
                "id": "",
                "pid": this.props.pid,
                "projectId": this.props.projectId,
                "treeType": "task",
                "masters": [],
                "children": null,
                "name": "",
                "rate": 0,
                "nodeCount": null,
	            "beginTime": null,
                "endTime":null,
                "orders": 0,
	            "remark": ""
            }), // 新增任务数据
            expandedKeys: [], // 折叠
            keys: [], // 折叠
            subtasks:[],
		});
		/*if(true){ // 暂时控制不住多次事件监听
			runonlyonce = true;
			this.emitterEvent = emitter.addListener('handletaskid', (obj)=>{
				console.log('触发监听')
				this.getKeyByShowRow(obj.pid, obj.id);
		   })
		}*/
		this.eventE2 = emitter.addListener('handletaskid', (obj)=>{
			this.getKeyByShowRow(obj.pid, obj.pid, obj.isEdit);
		});
	}

	componentWillUnMount(){
		emitter.removeListener(this.eventE2)
	}

	getKeyByShowRow = (key, id, isEdit) => {
		// 填报2  查看1
		if (isEdit == 2) {
			this.saveExpandedRows(key)
		} else {
			this.handleView(key);
		}
		this.scrollToAnchor(id);
	}

	// 锚点定位
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
            try {
                let anchorElement = document.getElementById(anchorName);
	            if (anchorElement) {
	                anchorElement.scrollIntoView({block: 'center'});
                }
            } catch(err) {
                console.log("scrollToAnchorERR=>"+err);
            }
		}
	}

	// 删除
	handleDelete = (record) => {
	    const {id, key} = record;
	    if (id!==""&&id!==null&&!id.startsWith("!")) { // 删除原来已有的
	        this.store.delNode(id).then((res)=>{
                if (res) {
                    message.success("删除成功");
                    const dataSource = [...this.state.dataSource];
                    this.setState({
                        dataSource: dataSource.filter(item => item.key !== key)
                    }, ()=>{
                        this.props.saveData(this.state.dataSource);
                        // 更新tree
                        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                        this.store.treeData = utils.updateTreeData(treeData, "del", record);
                    });
                    // 更新步骤
					this.store.getStageTree();
					//更新我的任务数
					this.store.getMyTaskCount();
                } else {
                    message.error("删除失败")
                }
            });
        } else { // 删除新增的
            const dataSource = [...this.state.dataSource];
            this.setState({
                dataSource: dataSource.filter(item => item.key !== key)
            }, ()=>{
                this.props.saveData(this.state.dataSource, key);
                // 更新tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "del", record);
            });
        }
	};

	// 新增
	handleAdd = () => {
		const {dataSource, primaryData, count} = this.state;
        const newData = Object.assign(primaryData, {key: count, id: "!"+uuidv4()});
        //console.log(newData, '新增')
        this.setState({
            dataSource: [...dataSource, newData],
            count: count + 1,
        }, ()=>{
            this.props.saveData(this.state.dataSource);
            // 更新tree
            let treeData = JSON.parse(JSON.stringify(this.store.treeData));
            this.store.treeData = utils.updateTreeData(treeData, "add", newData);
        });
	};

	// 保存
	handleSave = row => {
        const newData = [...this.state.dataSource];
        const index = newData.findIndex(item => row.key === item.key);
        const item = newData[index];
		newData.splice(index, 1, {
			...item,
			...row,
		});
		this.setState({dataSource: newData}, ()=>{
            // 更新保存数据
            this.props.saveData(this.state.dataSource);
            let newRow = JSON.parse(JSON.stringify({...row}));
            // 更新tree
            let treeData = JSON.parse(JSON.stringify(this.store.treeData));
            this.store.treeData = utils.updateTreeData(treeData, "update", newRow);
        });
	};

	// 折叠效果
	saveExpandedRows = (key) => {
	    let _this = this;
		const {keys} = _this.state;
		if (keys.indexOf(key) >= 0) {
			keys.splice(keys.indexOf(key), 1);
		} else {
			keys.push(key);
		}
        _this.setState({expandedKeys: keys},()=>{
           let obj = {[_this.props.id]: keys};
           let expandedKeys = JSON.parse(JSON.stringify(_this.store.expandedKeys));
           _this.store.expandedKeys = Object.assign(expandedKeys, obj);
        });
	}

	// 查看
	handleView = (record) => {
		this.setState({
			subTaskDis: true, // 不可编辑
		}, ()=>{
			this.saveExpandedRows(record);
		});
	}

	// 拆解
    handleApart = (record) => {
        // if (key==null||key=="") {
	     //    message.warn("请先保存主任务，主任务保存后才能拆解子任务");
	     //    return false;
        // }
        // 记录当前主任务没有改动的数据
        this.setState({
            activeObj: Object.assign({}, record),
            activeSub: Object.assign([], record.children==null?[]:record.children),
	        subTaskDis: false, // 可编辑
        }, ()=>{
            this.saveExpandedRows(record.id);
        });
    }

	// 保存子任务
	saveSubTaskRecord = (id) => {
	    // 如果子任务为空，保存是调用新街口
       let length = this.state.subtasks.length
	    if (length==0) {
           this.store.delNodeByPid({pid:id})
               .then((res)=>{
                  if (res) {
                      // 更新树
                      this.props.updateTree();
                      // 更新步骤
					  this.store.getStageTree();
					  //更新我的任务数
					  this.store.getMyTaskCount();
                      message.success("保存成功")
                      this.saveExpandedRows(id);
                  } else {
                      message.error("保存失败")
                  }
               });
        } else {
           // 子任务名称和负责人必填
            const subtasks = JSON.parse(JSON.stringify(this.state.subtasks));
            const isSub = subtasks.filter((item)=>{return item.name==""||item.masters==null||item.masters.length==0})
            if (isSub.length>0) {  // true
                message.warn("请您填写任务名称和负责人");
                return false;
            }
		    const isLarge = subtasks.filter((item)=>{return Number(item.alreadyHours)>Number(item.totalHours)})
		    if (isLarge.length>0) {  // true
			    message.warn("已用工时不能大于预估工时");
			    return false;
		    }
            // 判断是新增的主任务进行拆解的
            if (id.startsWith("!")) {
                this.saveExpandedRows(id);
            } else {
                // 原有
                this.store.saveSubTasks(this.state.subtasks)
                    .then((res)=>{
                        if (res&&res.length>0) {
                            message.success("保存成功")
                            this.subtask.clearState();
                            // 更新tree
							let treeData = JSON.parse(JSON.stringify(this.store.treeData));
							let record = utils.getRecord(treeData, id);
							// 如果存在 attachments 更新的时候要添加附件集
							let attachments = record&&record.children!=null&&record.children.filter((item)=>{return item.treeType==='attc'})[0];
							if(attachments){
								res.push(attachments);
							}
                            this.store.treeData = utils.updateTreeData(treeData, "updateChildren", res);
                            // 更新步骤
                            this.store.getStageTree();
                            this.saveExpandedRows(id);
                        } else {
                            message.error("保存失败")
                        }
                    });
            }
        }
	}

	// 更新子任务数据
	handleSaveSubTask = (subtasks) => {
		let newSubTasks = [];
		subtasks.forEach(item=>{
			let {key,children, ...obj} = item;
			newSubTasks.push(obj)
		})
		this.setState({subtasks:newSubTasks})
	}

	// 取消保存
    onCalac = (key)=> {
        this.saveExpandedRows(key);
        this.store.isEdit = false;
        // 更新tree
        let treeData = JSON.parse(JSON.stringify(this.store.treeData));
        this.store.treeData = utils.updateTreeData(treeData, "update", this.state.activeObj);
        this.subtask.clearState();
        this.setState({
            activeObj: Object.assign({})
        });
    }

    // 折叠收起
    onPackup = (record) => {
	    let _this = this;
	    const defaultSource = _this.state.activeSub.map(item=>{
	        return {
                "name": item.name,
                "masters": item.masters,
                "totalHours": item.totalHours,
                "alreadyHours": item.alreadyHours
	        }
        });  // 拆解前默认数据
        const rearSource = _this.state.subtasks.map(item=>{
            return {
                "name": item.name,
                "masters": item.masters,
                "totalHours": item.totalHours,
                "alreadyHours": item.alreadyHours
            }
        });  // 拆解后内容变动数据
       // console.log(JSON.stringify(defaultSource), JSON.stringify(rearSource), '===比较', JSON.stringify(defaultSource)==JSON.stringify(rearSource))
        // 当前拆解数据内容变化，给提示是否保存
        if (JSON.stringify(defaultSource)==JSON.stringify(rearSource)) {
            // 内容没有变动，直接取消
            _this.onCalac(record.id);
        } else {
            Modal.confirm({
                title: '是否对所更改内容保存',
                okText: '保存',
                cancelText: '不保存',
                onOk() {
                    _this.saveSubTaskRecord(record.id);
                },
                onCancel() {
                    _this.onCalac(record.id);
                },
            });
        }
    }

    onTaskRef = (ref) => {
        this.subtask = ref
    }

	// 点击
	onRow = (record) => {
		const {disabled} = this.props;
		let subTasks = record.children==null?[]:record.children.filter((item)=>{return item.treeType==='subtask'})
		const subNumber = subTasks==null||subTasks.length==0?0:subTasks.length
		if (disabled&&subNumber > 0) {  // 详情态
			this.handleView(record.id)
		}
	}

	render() {
		const {subTaskDis, dataSource, expandedKeys} = this.state;
		const {disabled} = this.props;
		const {taskhaveattt} = this.props;
		const isChaijie = expandedKeys.length>0;
		const components = {
			body: {
				row: EditableFormRow,
				cell: EditableCell,
			},
		};
		const columnEle = taskhaveattt?
		[
			{
				title: '任务',
				dataIndex: 'name',
				width: '12%',
				align: "center",
                editable: !disabled&&!isChaijie,
                render: (text, record) => {
				    return(<div
					          //  id={`/workdetail?projectid=${record.projectId}&id=${record.id}`}
					            id={record.id}
				                onClick={this.onRow.bind(this, record)}
				                style={{width: '100%', height: '46px', lineHeight: '46px'}}
				                className="pr">
                            <Icon className={record.delayType!==0&&disabled?"":"hidden"} style={{fontSize:'18px',position:'absolute', left: '5px', color: 'red', top: '50%', transform: 'translateY(-50%)'}} type="exclamation-circle" />
                            {text}
                           </div>);
                }
			},
			{
				title: '负责人',
				dataIndex: 'masters',
				width: '12%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						onClick={this.onRow.bind(this, record)}>{text==null?"":text.map(item=>item.userName).join("、")}</div>;
				},
			},
			{
				title: '开始日期',
				dataIndex: 'beginTime',
				width: '12%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render: (text, record) => {
					return <div
						style={{width: '100%', height: '46px', lineHeight: '46px'}}
						onClick={this.onRow.bind(this, record)}>{text==null||text==0?"":moment(text).format("YYYY-MM-DD")}</div>;
				},
			},
            {
                title: '截止日期',
                dataIndex: 'endTime',
                width: '12%',
                editable: !disabled&&!isChaijie,
                align: "center",
	            render: (text, record) => {
		            return <div
			            style={{width: '100%', height: '46px', lineHeight: '46px'}}
			            onClick={this.onRow.bind(this, record)}>{text==null||text==0?"":moment(text).format("YYYY-MM-DD")}</div>;
	            },
            },
			{
				title: '完成比例',
				dataIndex: 'rate',
				width: '12%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render:(text, record) => {return <div style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}>{text}%</div>}
			},
				{
				title: '对应产出',
				dataIndex: 'attc',
				width: '12%',
				align: "center",
                render: (text, record) => {
					let attcObj = record.children!=null&&record.children.filter((item)=>{return item.treeType==='attc'})[0];  //附件对象
					const attachments = attcObj&&attcObj.children&&attcObj.children!=null?attcObj.children.map((vitem) => {
						return Object.assign({}, {
							uid: vitem.id,
							name: vitem.fileName,
							status: 'done',
							url: vitem.filePath,
							fileId: vitem.fileId,
							fileName:vitem.fileName,
							filePath: vitem.filePath,
							id: vitem.id
						})
					}) : [];
                    return (
                    	<div className="table_upload">
		                    {attcObj==false?
		                    "- -":<MyUpload
				                    saveAttachment={this.props.saveAttachment}
				                    delNode={this.props.delNode}
				                    fileList={attachments}
				                    pid={attcObj?attcObj.id:""}
				                    projectId={attcObj?attcObj.projectId:""}
				                    disabled={disabled||isChaijie}
				                    ossData={this.props.oss}
				                    multiple={false}
			                    />}
                    	</div>)
				}
			},
			{
				title: '备注',
				dataIndex: 'remark',
				width: '12%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render:(text, record) => {
					const content = (
						<div>
							{text==null||text==""?null:<p><span style={{fontWeight:'700',color: '#333'}}>整体:</span> {text}</p>}
							{record.children==null||record.children==[]?null:record.children.filter((item)=>{return item.treeType==='subtask'}).map((item, index)=>{
								return (<p className={item.remark==null||item.remark==""?"hidden":""} key={'task_'+index}><span style={{fontWeight:'700',color: '#333'}}>备注{index+1}:</span> {item.remark}</p>)
							})}
						</div>
					);
					return (
						<div
							style={{width: '100%', height: '46px', lineHeight: '46px'}}
							onClick={this.onRow.bind(this, record)}
						>
							<Popover content={content} trigger="hover">
								<div
									style={{maxWidth: '180px'}}
									className="ellsis">
									{text==null||text==""?null:<span><span style={{fontWeight:'700',color: '#333'}}>整体:</span> {text}</span>}
									{record.children==null||record.children==[]?null:record.children.filter((item)=>{return item.treeType==='subtask'}).map((item, index)=>{
										return (<span key={'task_'+index} className={item.remark==null||item.remark==""?"hidden":""}>
		                                  <span style={{paddingLeft: '5px',fontWeight:'700',color: '#333'}}>
		                                  备注{index+1}:</span> {item.remark}</span>)
									})}
								</div>
							</Popover>
						</div>
					)}
			},
			{
				title: '排期',
				dataIndex: 'operation',
				align: "center",
				render: (text, record) => {
					let actionName = '拆解';
					if (expandedKeys.filter(item => {return item == record.id}).length>0&&!subTaskDis) {
						return (<span>
							       <a onClick={this.onCalac.bind(this, record.id)}>取消</a>
								   <a className="ml15" onClick={this.saveSubTaskRecord.bind(this, record.id)}>保存</a>
						       </span>)
					}
				    else {
						// 子任务数
						let subTasks = record.children==null?[]:record.children.filter((item)=>{return item.treeType==='subtask'})
                        const subNumber = subTasks==null||subTasks.length==0?0:subTasks.length
                        if (disabled) {  // 详情态
	                        if (subNumber==0) { // 子任务数等于0
		                        return <a onClick={this.handleApart.bind(this, record)}>拆解</a>
		                        //return <span>{subNumber}</span>
	                        } else {  // 子任务数大于0
		                        return (<span>
	                                <a title="查看" onClick={this.handleView.bind(this, record.id)}>{subNumber}</a>
	                                <Icon onClick={this.handleApart.bind(this, record)} className="pointer" style={{marginLeft:"10px"}} title="编辑" type="edit" />
                                </span>)
	                        }
                        } else {  // 编辑态
                            return (<a  onClick={this.handleApart.bind(this, record)}>{subNumber==0?actionName:subNumber}</a>)
                        }
					}
				}
			},
		]:[
			{
				title: '任务',
				dataIndex: 'name',
				width: '14%',
				align: "center",
                editable: !disabled&&!isChaijie,
                render: (text, record) => {
				    return(<div
					            id={record.id}
					            //id={`/workdetail?projectid=${record.projectId}&id=${record.id}`}
				                onClick={this.onRow.bind(this, record)}
				                style={{width: '100%', height: '46px', lineHeight: '46px'}}
				                className="pr">
                            <Icon className={record.delayType!==0&&disabled?"":"hidden"} style={{fontSize:'18px',position:'absolute', left: '5px', color: 'red', top: '50%', transform: 'translateY(-50%)'}} type="exclamation-circle" />
                            {text}
                           </div>);
                }
			},
			{
				title: '负责人',
				dataIndex: 'masters',
				width: '14%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render: (text, record) => {
					return <div style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}>{text==null?"":text.map(item=>item.userName).join("、")}</div>;
				},
			},
			{
				title: '开始日期',
				dataIndex: 'beginTime',
				width: '14%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render: (text, record) => {
					return <div style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}>{text==null||text==0?"":moment(text).format("YYYY-MM-DD")}</div>;
				},
			},
            {
                title: '截止日期',
                dataIndex: 'endTime',
                width: '14%',
                editable: !disabled&&!isChaijie,
                align: "center",
	            render: (text, record) => {
		            return <div style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}>{text==null||text==0?"":moment(text).format("YYYY-MM-DD")}</div>;
	            },
            },
			{
				title: '完成比例',
				dataIndex: 'rate',
				width: '14%',
				editable: !disabled&&!isChaijie,
				align: "center",
				render:(text, record) => {return <div style={{width: '100%', height: '46px', lineHeight: '46px'}} onClick={this.onRow.bind(this, record)}>{text}%</div>}
			},
				{
					title: '备注',
					dataIndex: 'remark',
					width: '14%',
					editable: !disabled&&!isChaijie,
					align: "center",
					render:(text, record) => {
						const content = (
							<div>
								{text==null||text==""?null:<p><span style={{fontWeight:'700',color: '#333'}}>整体:</span> {text}</p>}
								{record.children==null||record.children==[]?null:record.children.filter((item)=>{return item.treeType==='subtask'}).map((item, index)=>{
									return (<p className={item.remark==null||item.remark==""?"hidden":""} key={'task_'+index}><span style={{fontWeight:'700',color: '#333'}}>备注{index+1}:</span> {item.remark}</p>)
								})}
							</div>
						);
						return (
							<div
								style={{width: '100%', height: '46px', lineHeight: '46px'}}
								onClick={this.onRow.bind(this, record)}>
								<Popover content={content} trigger="hover">
									<div
										style={{maxWidth: '180px'}}
										className="ellsis">
										{text==null||text==""?null:<span><span style={{fontWeight:'700',color: '#333'}}>整体:</span> {text}</span>}
										{record.children==null||record.children==[]?null:record.children.filter((item)=>{return item.treeType==='subtask'}).map((item, index)=>{
											return (<span key={'task_'+index} className={item.remark==null||item.remark==""?"hidden":""}>
		                                  <span style={{paddingLeft: '5px',fontWeight:'700',color: '#333'}}>
		                                  备注{index+1}:</span> {item.remark}</span>)
										})}
									</div>
								</Popover>
							</div>
						)}
				},
			{
				title: '排期',
				dataIndex: 'operation',
				align: "center",
				render: (text, record) => {
					let actionName = '拆解';
					if (expandedKeys.filter(item => {return item == record.id}).length>0&&!subTaskDis) {
						return (<span>
							       <a onClick={this.onCalac.bind(this, record.id)}>取消</a>
								   <a className="ml15" onClick={this.saveSubTaskRecord.bind(this, record.id)}>保存</a>
						       </span>)
					}
				    else {
						// 子任务数
						let subTasks = record.children==null?[]:record.children.filter((item)=>{return item.treeType==='subtask'})
                        const subNumber = subTasks==null||subTasks.length==0?0:subTasks.length
                        if (disabled) {  // 详情态
	                        if (subNumber==0) { // 子任务数等于0
		                        return <a onClick={this.handleApart.bind(this, record)}>拆解</a>
		                        //return <span>{subNumber}</span>
	                        } else {  // 子任务数大于0
		                        return (<span>
	                                <a title="查看" onClick={this.handleView.bind(this, record.id)}>{subNumber}</a>
	                                <Icon onClick={this.handleApart.bind(this, record)} className="pointer" style={{marginLeft:"10px"}} title="编辑" type="edit" />
                                </span>)
	                        }
                        } else {  // 编辑态
                            return (<a  onClick={this.handleApart.bind(this, record)}>{subNumber==0?actionName:subNumber}</a>)
                        }
					}
				}
			}
		];
		const columns = columnEle.map(col => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: record => ({
					record,
					editable: col.editable,
					dataIndex: col.dataIndex,
					title: col.title,
					personData: mobx.toJS(this.store.personData),
					handleSave: this.handleSave,
                    handleDelete: this.handleDelete,
				}),
			};
		});
		const expandedRowRender = (record) => {
			// 新增子任务
			let addRecord={
				id: "",
				pid: record.id||"",
				projectId: record.projectId||"",
				treeType: "subtask",
				masters: record.masters==null?[]:record.masters,
				alreadyHours: 0,
				totalHours: 0,
				rate: 0,
				name: "",
				remark: "",
				beginTime: null,
				endTime: null
			}
			return (
				<TaskSplite
                    store={this.store}
					record={record}
                    addRecord={addRecord}
					personData={mobx.toJS(this.store.personData)}
					dataSource={record.children==null?[]:record.children.filter((item)=>{return item.treeType==='subtask'})}
					collapseOnChange={() => isChaijie&&!disabled?this.onPackup(record):this.saveExpandedRows(record.id)}
					handleSaveSubTask={this.handleSaveSubTask}
                    disabled={subTaskDis}
                    id={record.id}
                    onTaskRef={this.onTaskRef}
                />
			)
		};
		return (
			<div>
				<Table
					components={components}
					rowClassName={() => 'editable-row'}
                    className="expand_table bpm_table"
					bordered={false}
					dataSource={dataSource}
					columns={columns}
					pagination={false}
					size={"middle"}
					expandRowByClick={true}
					expandIconAsCell={false}
					expandIconColumnIndex={-1}
					expandedRowKeys={expandedKeys}
					expandedRowRender={expandedRowRender}
					rowKey={row => row.id}
				/>
                {disabled?null:<Icon onClick={this.handleAdd} style={{margin: '16px 0', color: '#4BA4BE', fontSize: '22px'}} type="plus-circle"/>}
			</div>
		);
	}
}

export default EditableTable2;




