import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Tooltip, Input} from 'antd';
//import moment from 'moment';
//import MyUpload from "@/components/worktile/MyUpload";
import PersonAdd2 from "@/components/worktile/PersonAdd";

// import AsyncSelectMuti from "@/components/common/AsyncSelectMuti";
// import authSchemas from "@/utils/Schemas/authorization";
// import Config from "@/config";
import * as utils from "@/utils/utils";

const formItemLayout = {
	labelCol: {span: 11},
	wrapperCol: {span: 13},
}
const CustomizedForm = Form.create({
	name: 'proposalattc',
	onFieldsChange(props, changedFields) {
		props.onChange(changedFields);
	},
	mapPropsToFields(props) {
		return {
			supplierPrice: Form.createFormField({
				...props.supplierPrice,
				value: props.supplierPrice.value,
			}),
			customerPrice: Form.createFormField({
				...props.customerPrice,
				value: props.customerPrice.value,
			}),
			upload: Form.createFormField({
				...props.upload,
				value: props.upload.value,
			}),
			masters: Form.createFormField({
				...props.masters,
				value: props.masters.value,
			}),
		}
	},
	onValuesChange(_, values) {
		// console.log(values);
	}
})(props => {
	const {getFieldDecorator} = props.form;
	const {onSave, onBlur, attcObj, saveAttachment, delNode, oss} = props;
	// const attachments = attcObj.children!=null?attcObj.children.map((vitem, index) => {
	//     return Object.assign({}, {
	//         uid: vitem.id,
	//         name: vitem.fileName,
	//         status: 'done',
	//         url: vitem.filePath,
	//         fileId: vitem.fileId,
	//         fileName:vitem.fileName,
	//         filePath: vitem.filePath,
	//         id: vitem.id
	//     })
	// }) : [];

	return (
		<Form {...formItemLayout} layout="horizontal">
			{/* <Form.Item label="内容留存"
                       labelCol={{
                           xs: { span: 24 },
                           sm: { span: 2},
                       }}
                       wrapperCol={{
                           xs: { span: 24 },
                           sm: { span: 22 },
                       }}
            >
               <MyUpload
                    saveAttachment={saveAttachment}
                    delNode={delNode}
                    fileList={attachments}
                    pid={attcObj.id}
                    projectId={attcObj.projectId}
                    disabled={props.isEdit}
                    ossData={oss}
               />
            </Form.Item>*/}
			<Form.Item label={props.proposalLabel}
			           labelCol={{
				           xs: {span: 24},
				           sm: {span: 2},
			           }}
			           wrapperCol={{
				           xs: {span: 24},
				           sm: {span: 22},
			           }}
			>
				<Form.Item
					style={{width: '250px'}}
					className="prop_attr"
					label={props.supplierLabel}>
					{getFieldDecorator('supplierPrice', {
						initialValue: props.supplierPrice.value==null||props.supplierPrice.value==0?"":props.supplierPrice.value,
						rules: [{required: false, message: '字段不能为空'}],
					})(
						<span className="w">
							<Tooltip
								placement="topLeft"
								trigger={['focus']}
								title={props.supplierPrice.value>1000?"金额单位是万元":null}>
                            <Input style={{width: '60px', marginRight: '5px'}}
                                   disabled={props.isEdit}
                                   value={props.supplierPrice.value==null||props.supplierPrice.value==0?"":props.supplierPrice.value}/><span>万元</span>
							</Tooltip>
                        </span>
					)}
				</Form.Item>
				<Form.Item
					style={{width: '250px'}}
					className="prop_attr"
					label={props.customerLabel}>
					{getFieldDecorator('customerPrice', {
						initialValue: props.customerPrice.value==null||props.customerPrice.value==0?"":props.customerPrice.value,
						rules: [{required: false, message: '字段不能为空'}],
					})(
						<span className="w">
                            <Tooltip
	                            placement="topLeft"
	                            trigger={['focus']}
	                            title={props.customerPrice.value>1000?"金额单位是万元":null}>
                            <Input style={{width: '60px', marginRight: '5px'}}
                                   disabled={props.isEdit}
                                   value={props.customerPrice.value==null||props.customerPrice.value==0?"":props.customerPrice.value}/><span>万元</span>
                            </Tooltip>
                        </span>
					)}
				</Form.Item>
			</Form.Item>
			<Form.Item label="报价同步"
			           labelCol={{
				           xs: {span: 24},
				           sm: {span: 2},
			           }}
			           wrapperCol={{
				           xs: {span: 24},
				           sm: {span: 6},
			           }}
			           className="w">
				{getFieldDecorator('master999', {
					initialValue: props.masters.value.length > 0 ? props.masters.value.length > 0 && props.masters.value.map((item) => {
						return item.userId
					}) : [],
					rules: [{required: false, message: '请选择同步人'}],
				})(
					<div className="w">
						<PersonAdd2
							data={props.masters.value.length > 0 ? props.masters.value.length > 0 && props.masters.value : []}
							updatePerson={onSave}
							disabled={props.isEdit}
						/>
						{/*<AsyncSelectMuti
                        className="w"
                        type="getUserInfoListByRoleCode"
                        variables={{
                            appId: "bocspace",
                            code: "bocspace.group",
                            pageIndex: 1,
                            pageSize: 10000,
                        }}
                        query={authSchemas.authorization.getUserInfoListByRoleCode}
                        url={Config.authorization.getUserInfoListByRoleCode}
                        onChange={onSave}
                        itemKey="id"
                        itemValue="realname"
                        body="body"
                       disabled={props.isEdit}
                    />*/}
					</div>
				)}
			</Form.Item>
		</Form>
	);
});

@observer
export default class ProposalAttc extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			isEdit: false,
			data: this.props.data,
			fields: {
				supplierPrice: {
					value: this.props.data.filter((item) => {
						return item.treeType === 'proposal'
					})[0]['supplierPrice'] || 0,
				},
				customerPrice: {
					value: this.props.data.filter((item) => {
						return item.treeType === 'proposal'
					})[0]['customerPrice'] || 0,
				},
				upload: {
					value: ""
				},
				masters: {
					value: this.props.data.filter((item) => {
						return item.treeType === 'proposal'
					})[0]['masters'] == null ? [] : this.props.data.filter((item) => {
						return item.treeType === 'proposal'
					})[0]['masters']
				}
			}
		}
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.data != this.props.data) {
			this.setState({
				data: nextProps.data,
			})
			let obj = {};
			obj.supplierPrice = {
				"value": nextProps.data.filter((item) => {
					return item.treeType === 'proposal'
				})[0]['supplierPrice'],
			}
			obj.customerPrice = {
				"value": nextProps.data.filter((item) => {
					return item.treeType === 'proposal'
				})[0]['customerPrice'],
			}
			obj.masters = {
				"value": nextProps.data.filter((item) => {
					return item.treeType === 'proposal'
				})[0]['masters'] == null ? [] : nextProps.data.filter((item) => {
					return item.treeType === 'proposal'
				})[0]['masters']
			}
			this.setState(({fields}) => ({
				fields: {...fields, ...obj},
			}));
		}
	}

	onChangeVis = () => {
		this.setState({
			isEdit: !this.state.isEdit
		});
	}

	handleFormChange = changedFields => {
		this.setState(({fields}) => ({
			fields: {...fields, ...changedFields},
		}), () => {
			let proposalObj = this.state.data.filter((item) => {
				return item.treeType === 'proposal'
			})[0]
			let params = Object.assign({}, proposalObj, {"supplierPrice": this.state.fields.supplierPrice.value}, {"customerPrice": this.state.fields.customerPrice.value})
			this.saveData(params);
			// 更新tree
			let treeData = JSON.parse(JSON.stringify(this.store.treeData));
			this.store.treeData = utils.updateTreeData(treeData, "update", params);
		});
	}

	saveData = (params) => {
		this.props.saveProposal(params);
	}

	// 报价同步
	save2 = (val, e) => {
		let userIds;
		if (e.length) {
			userIds = e.map((item) => {
				return {userId: item.props.value, userName: item.props.children}
			})
		} else {
			userIds = []
		}
		const {data} = this.state;
		let proposalObj = data.filter((item) => {
			return item.treeType === 'proposal'
		})[0];
		let params = Object.assign({}, proposalObj, {"masters": userIds});
		this.props.saveNodes(params);
		// this.saveData(params)
	}

	//
	save = (users) => {
		const {data} = this.state;
		let proposalObj = data.filter((item) => {
			return item.treeType === 'proposal'
		})[0];
		let params = Object.assign({}, proposalObj, {"masters": users});
		this.props.saveNodes(params);
	}

	render() {
		const {stepName, disabled, oss} = this.props;
		const {data, isEdit, fields} = this.state;

		let proposalLabel = "";
		let supplierLabel = "";
		let customerLabel = "";
		let formtype = "";
		if (stepName === "2. 汇报确定") {
			proposalLabel = "初步报价";
			supplierLabel = "供应商预估";
			customerLabel = "客户预估";
			formtype = "estimate";
		}
		if (stepName === "4. 图纸确认") {
			proposalLabel = "精确报价";
			supplierLabel = "供应商报价";
			customerLabel = "客户报价";
			formtype = "precise";
		}

		const labelObj = {
			"proposalLabel": proposalLabel,
			"supplierLabel": supplierLabel,
			"customerLabel": customerLabel
		};

		const editStatus = {
			"isEdit": disabled
		}

		// const attcObj = data.filter((item)=>{return item.treeType==='attc'})[0]; // 附件数据

		return (
			<div className="mt15">
				<CustomizedForm
					//oss={oss}
					//attcObj={attcObj}
					{...fields}
					{...labelObj}
					{...editStatus}
					onChange={this.handleFormChange}
					onSave={this.save}
					//delNode={this.props.delNode}
					//saveAttachment={this.props.saveAttachment}
				/>
			</div>
		)
	}
}




